export class ServerError {
  static Failed = 'failed'
  static FormRequestError = 'Form request param error'
  static JwtExpire = 'expired jwt token'
  static PasswordError = 'crypto/bcrypt: hashedPassword is not the hash of the given password'
  static CaptchaError ='WRONG_RESPONSE'
  static CaptchaExpired ='RESPONSE_USED'

  static UserNotMatch = 'user not match'
  static UserNotFound = 'user not found'
  static UserChangePassFailed = 'user change password failed'

  static UserInviteRequired = 'user_invite code required'
  static UserInviteNotFound = 'user_invite not found'
  static UserInviteUnavailable = 'user_invite unavailable'
  static UserInviteCreateFailed = 'user_invite create failed'

  static UserInfoNotFound = 'user_info not found'
  static UserInfoExisted = 'user_info existed'
  static UserInfoCreateFailed = 'user_info create failed'
  static UserInfoChangeFailed = 'user_info change failed'
  static UserInfoNothingChange = 'user_info nothing change'

  static UserRegisterFailed = 'user register failed'
  static UserNameExist = 'user_name has existed'
  static EmailExist = 'email has existed'
  static PhoneExist = 'phone has existed'

  static AccountNotFound = 'account not found'
  static AccountBanned = 'account was banned'
  static AccountNameRequired = 'account name required'

  static ExamNotFound = 'exam not found'
  static ExamCreateFailed = 'exam create failed'
  static PaperNotFound = 'paper not found'
  static QuestionNotFound = 'question not found'
  static SectionNotFound = 'section not found'
  static PaperDisabled = 'paper disabled'

  static TokenNotFound = 'token not found'
  static TokenCreateFailed = 'token create failed'
  static TokenUpdateFailed = 'token update failed'

  static TicketNotFound = 'ticket not found'
  static TicketCreateFailed = 'ticket create failed'
  static TicketReplyFailed = 'ticket reply failed'
  static TicketCloseFailed = 'ticket close failed'
  static TicketEvaluateFailed = 'ticket evaluate failed'
  static TicketContentCreateFailed = 'ticket content create failed'

  static WalletNotFound = 'wallet not found'
  static WalletExisted = 'wallet existed'
  static WalletCoinLimit = 'wallet coin limit'
  static WalletCreateFailed = 'wallet create failed'
  static WalletUpdateFailed = 'wallet update failed'
  static WalletLogNotFound = 'wallet_log not found'
  static WalletLogCreateFailed = 'wallet_log create failed'
  static PaymentNotFound = 'payment not found'
  static PaymentCreateFailed = 'payment create failed'
  static PaymentUpdateFailed = 'payment update failed'
  static ProductNotFound = 'product not found'
  static ProductLevelLimit = 'product level limit'
  static ProductDisabled = 'product disabled'
  static ProductCreateFailed = 'product create failed'
  static ProductUpdateFailed = 'product update failed'
  static PurchaseNotFound = 'purchase not found'
  static PurchaseCreateFailed = 'purchase create failed'
  static PurchaseUpdateFailed = 'purchase update failed'

  static Translate (code) {
    switch (code) {
      case ServerError.Failed:
        return '操作失败，未知错误'
      case ServerError.FormRequestError:
        return '表单提交错误'
      case ServerError.JwtExpire:
        return '登录已过期'
      case ServerError.PasswordError:
        return '密码错误'
      case ServerError.CaptchaError:
        return '验证码错误'
      case ServerError.CaptchaExpired:
        return '验证码过期'
      case ServerError.UserNotMatch:
        return '账户未匹配'
      case ServerError.UserNotFound:
        return '账户未找到'
      case ServerError.UserChangePassFailed:
        return '修改密码失败'
      case ServerError.UserInviteRequired:
        return '请输入邀请码'
      case ServerError.UserInviteNotFound:
        return '未找到邀请码'
      case ServerError.UserInviteUnavailable:
        return '邀请码不可用'
      case ServerError.UserInviteCreateFailed:
        return '邀请码创建失败'
      case ServerError.UserInfoNotFound:
        return '用户信息未找到'
      case ServerError.UserInfoChangeFailed:
        return '用户信息修改失败'
      case ServerError.UserInfoNothingChange:
        return '用户信息没有需要修改的'
      case ServerError.UserRegisterFailed :
        return '账号注册失败'
      case ServerError.UserNameExist:
        return '该用户名已存在'
      case ServerError.EmailExist:
        return '该邮箱已存在'
      case ServerError.PhoneExist:
        return '该手机号已存在'
      case ServerError.AccountNotFound:
        return '账户未找到'
      case ServerError.AccountBanned:
        return '账户被封禁'
      case ServerError.AccountNameRequired:
        return '请填写账户名称'
      case ServerError.ExamNotFound:
        return '该场测试未找到'
      case ServerError.ExamCreateFailed:
        return '该场测试保存失败'
      case ServerError.PaperNotFound:
        return '试卷未找到'
      case ServerError.QuestionNotFound:
        return '试题未找到'
      case ServerError.SectionNotFound:
        return '试卷板块未找到'
      case ServerError.PaperDisabled:
        return '试卷已停用'
      case ServerError.TokenNotFound:
        return '令牌未找到'
      case ServerError.TokenCreateFailed:
        return '令牌创建失败，请联系管理员'
      case ServerError.TicketNotFound:
        return '工单未找到'
      case ServerError.TicketCreateFailed:
        return '工单创建失败'
      case ServerError.TicketReplyFailed:
        return '工单回复失败'
      case ServerError.TicketCloseFailed:
        return '工单关闭失败'
      case ServerError.TicketEvaluateFailed:
        return '工单评价失败'
      case ServerError.TicketContentCreateFailed:
        return '工单回复失败'
      case ServerError.WalletNotFound:
        return '钱包未找到'
      case ServerError.WalletCreateFailed:
        return '钱包创建失败，请联系管理员'
      case ServerError.WalletUpdateFailed:
        return '钱包更新失败，请联系管理员'
      case ServerError.WalletLogNotFound:
        return '消费日志未找到'
      case ServerError.WalletLogCreateFailed:
        return '消费日志'
      case ServerError.PaymentNotFound:
        return '订单未找到'
      case ServerError.PaymentCreateFailed:
        return '订单创建失败'
      case ServerError.PaymentUpdateFailed:
        return '订单更新失败'
      case ServerError.ProductNotFound:
        return '商品未找到'
      case ServerError.ProductLevelLimit:
        return '没有权限购买'
      case ServerError.ProductDisabled:
        return '商品已关闭购买'
      case ServerError.PurchaseNotFound:
        return '未找到购买记录'
      default:
        return code
    }
  }
}



