<template>
  <section class="user-center">
    <el-container>
      <el-header>
        <h1 class="title">Online Exam</h1>
        <el-dropdown class="dropdown">
          <div class="setting"><i class="el-icon-setting"></i> <span>{{user.nickname}}</span><i
            class="el-icon-caret-bottom"></i></div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item><span @click="onModifyPassword()">修改密码</span></el-dropdown-item>
            <el-dropdown-item><span @click="onLogout()">退出系统</span></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <el-container class="container">
        <el-aside width="200px">
          <el-menu @select="handleSelect" :default-active="activePath">
            <el-submenu index="1">
              <template slot="title"><i class="el-icon-document"></i>在线考试</template>
              <el-menu-item index="/user-center/exam-list">
                模拟考试
              </el-menu-item>
              <el-menu-item index="/user-center/completed-exam">
                已考场次
              </el-menu-item>
            </el-submenu>
            <el-submenu index="2">
              <template slot="title"><i class="el-icon-document"></i>用户面板</template>
              <el-menu-item index="/user-center/user-panel">
                我的信息
              </el-menu-item>
              <el-menu-item index="/user-center/purchase">
                会员升级
              </el-menu-item>
              <el-menu-item index="/user-center/suggestions-list">
                意见建议
              </el-menu-item>
              <el-menu-item index="/user-center/recharge-history">
                充值记录
              </el-menu-item>
              <el-menu-item index="/user-center/purchase-history">
                消费记录
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main class="main">
          <router-view></router-view>
          <modify-password :visible.sync="modifyPassword"></modify-password>
        </el-main>
      </el-container>
    </el-container>
  </section>
</template>
<script>
import ModifyPassword from '../account/ModifyPassword'
import {AccountService, UserService, WalletService} from '@/service/api'

export default {
  name: 'account-home',
  components: {
    'modify-password': ModifyPassword
  },
  data () {
    return {
      modifyPassword: false,
      activePath: this.$route.path
    }
  },
  computed: {
    user: function () {
      return this.$store.state.user
    }
  },
  mounted: function () {
    UserService.me()
    WalletService.me()
  },
  methods: {
    handleSelect: function (key) {
      this.$router.push({path: key})
    },
    onModifyPassword: function () {
      this.modifyPassword = true
    },
    onLogout: function () {
      AccountService.logout()
        .then(res => {
          if (res === 'success') {
            this.$message.success('已安全退出')
          } else {
            this.$message.error('退出发生异常，已强制退出')
          }
          this.$router.push({path: '/account-home/login'})
        })
    }
  }
}
</script>

<style scoped lang="scss">
  .user-center {
    .title {
      display: inline;
      position: absolute;
      left: 0;
      margin: 0 22px;
    }
    .el-header {
      background-color: #545c64;
      color: white;
      line-height: 60px;
      text-align: right;
      .dropdown {
        color: white;
        .setting {
          cursor: pointer;
          .el-icon-setting:first-child {
            font-size: 22px;
            vertical-align: middle;
          }
        }
      }

    }
    .container {
      height: calc(100vh - 80px);
      .main{
        overflow: hidden;
      }
    }
  }
</style>



