<template>
  <section class="reply">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0px">
      <el-form-item prop="content">
        <el-input type="textarea" v-model="ruleForm.content" rows="5" placeholder="请输入内容"></el-input>
      </el-form-item>
      <el-form-item>
        <el-upload ref="uploader"
                   action="http://upload-z2.qiniup.com"
                   :data="token"
                   :limit="Number(5)"
                   :before-upload="beforeUpload"
                   list-type="picture-card">
          <i class="el-icon-plus"></i>
          <div slot="tip">最多上传 5 张图片</div>
        </el-upload>
      </el-form-item>
    </el-form>
    <el-button-group style="width: 100%;margin-top: 0.5em;">
      <el-button type="primary" @click="submitForm('ruleForm')" style="width:66%">提交回复</el-button>
      <el-button @click="onHidden('ruleForm')" style="width:34%">我再想想</el-button>
    </el-button-group>
  </section>
</template>
<script>
import {SecurityService} from '@/service/api'

export default {
  name: 'Reply',
  props: [],
  computed: {},
  data () {
    return {
      token: null,
      ruleForm: {
        content: ''
      },
      rules: {
        content: [
          {required: true, message: '请输入内容', trigger: 'change'},
          {min: 2, max: 1024, message: '长度在 2 到 1024 个字符', trigger: 'change'}
        ]
      }
    }
  },
  methods: {
    beforeUpload: function () {
      if (this.token) {
        this.token.resource_key = 'tmp/' + new Date() + '.jpg'
        return true
      }
      const self = this
      return new Promise(function (resolve, reject) {
        SecurityService.qiniuToken()
          .then(res => {
            if (res) {
              self.token = {
                token: res.qiniu,
                resource_key: 'tmp/' + new Date() + '.jpg'
              }
              resolve()
            } else {
              self.$message.error('上传令牌获取失败')
              reject(res)
            }
          })
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        let imgs = []
        // todo ticket上传的图片，定义名称前增加 tmp/ 前缀 如 tmp/asda2dasd.jpg
        for (let f of this.$refs.uploader.uploadFiles) {
          imgs.push(f.response.key)
        }
        this.$emit('on-reply', {
          content: this.ruleForm.content,
          pics: imgs
        })
        this.$refs[formName].resetFields()
        this.$refs.uploader.clearFiles()
      })
    },
    onHidden: function (formName) {
      this.$refs[formName].resetFields()
      this.$refs.uploader.clearFiles()
      this.$emit('on-reply', null)
    }
  }
}

</script>

<style scoped lang="scss">
  .reply {
    border-radius: 0 1em;
    padding: 1em;
    box-shadow: 0 0 1em grey;
    .el-form {
      height: calc(100% - 3em);
      overflow-y: auto;
    }
  }
</style>



