import Vue from 'vue'
import Router from 'vue-router'
import store from '@/service/store'
import * as types from '@/service/types'
import AccountHome from '@/components/account/AccountHome'
import Login from '@/components/account/Login'
import Register from '@/components/account/Register'
import UserCenter from '@/components/user-center/UserCenter'
import UserPanel from '@/components/user-center/user-panel/UserPanel'
import Purchase from '@/components/user-center/user-panel/purchase/Purchase'
import MessageList from '@/components/user-center/user-panel/message/MessageList'
import NotifyList from '@/components/user-center/user-panel/notifies/NotifyList'
import PurchaseHistory from '@/components/user-center/user-panel/purchase/PurchaseHistory'
import RechargeHistory from '@/components/user-center/user-panel/recharge/RechargeHistory'
import Suggestions from '@/components/user-center/user-panel/suggestions/Suggestions'
import SuggestionsList from '@/components/user-center/user-panel/suggestions/SuggestionsList'
import SuggestionsView from '@/components/user-center/user-panel/suggestions/SuggestionsView'
import OnlineExamList from '@/components/user-center/online-exam-center/OnlineExamList'
import OnlineExamCenter from '@/components/user-center/online-exam-center/online-exam/OnlineExamCenter'
import OnlineExamGuide from '@/components/user-center/online-exam-center/online-exam/OnlineExamGuide'
import ExamPre from '@/components/user-center/online-exam-center/online-exam/ExamPrepare'
import ExamRoom from '@/components/user-center/online-exam-center/online-exam/ExamRoom'
import CompletedExamList from '@/components/user-center/completed-exam/CompletedExamList'
import CompletedExamView from '@/components/user-center/completed-exam/CompletedExamView'

Vue.use(Router)

const router = new Router({
  mode: 'history', // 如不支持，vue-router会自动降级处理
  routes: [{
    path: '/',
    redirect: '/account-home/login'
  }, {
    path: '/account-home',
    name: 'AccountHome',
    component: AccountHome,
    children: [{
      path: 'login',
      name: 'Login',
      component: Login
    }, {
      path: 'register',
      name: 'Register',
      component: Register
    }]
  }, {
    path: '/user-center',
    name: 'UserCenter',
    meta: {
      requireAuth: true
    },
    component: UserCenter,
    children: [{
      // 当 /user/:id/profile 匹配成功，
      // UserProfile 会被渲染在 User 的 <router-view> 中
      name: 'ExamList',
      path: 'exam-list',
      component: OnlineExamList,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'user-panel',
      component: UserPanel,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'purchase',
      component: Purchase,
      meta: {
        requireAuth: true
      }
    }, {
      name: 'PurchaseHistory',
      path: 'purchase-history',
      component: PurchaseHistory,
      meta: {
        requireAuth: true
      }
    }, {
      name: 'RechargeHistory',
      path: 'recharge-history',
      component: RechargeHistory,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'message/message-list',
      component: MessageList,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'notifies/notify-list',
      component: NotifyList,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'completed-exam',
      component: CompletedExamList,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'suggestions-list',
      component: SuggestionsList,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'suggestions',
      component: Suggestions,
      meta: {
        requireAuth: true
      }
    }, {
      path: 'suggestions-view/:id',
      component: SuggestionsView,
      meta: {
        requireAuth: true
      }
    }]
  }, {
    // 当 /user/:id/profile 匹配成功，
    // UserProfile 会被渲染在 User 的 <router-view> 中
    path: '/completed-exam-view/:id',
    name: 'CompletedExamView',
    meta: {
      requireAuth: true
    },
    component: CompletedExamView
  }, {
    path: '/exam-center/:id/',
    name: 'OnlineExamCenter',
    meta: {
      requireAuth: true
    },
    component: OnlineExamCenter,
    children: [{
      path: 'exam-pre',
      name: 'ExamPre',
      component: ExamPre
    }, {
      path: 'exam-room',
      name: 'ExamRoom',
      component: ExamRoom
    }, {
      // 当 /user/:id/profile 匹配成功，
      // UserProfile 会被渲染在 User 的 <router-view> 中
      path: 'exam-guide',
      name: 'ExamGuide',
      component: OnlineExamGuide
    }]
  }]
})

// 页面刷新时，重新赋值token
let jwt = window.localStorage.getItem('jwt')
if (jwt) {
  let exp = window.localStorage.getItem('exp')
  if (new Date().getTime() + 5000 < exp) {
    store.commit(types.LOGIN, jwt)
    store.commit(types.EXP, exp)
  } else {
    store.commit(types.LOGOUT)
  }
}

let user = window.localStorage.getItem('user')
user && store.commit(types.USER, JSON.parse(user))

router.beforeEach((to, from, next) => {
  if (store.state.jwt) {
    if (to.name === 'Login') {
      next('/user-center/exam-list')
    } else {
      next()
    }
  } else if (to.matched.some(r => r.meta.requireAuth)) {
    next({
      path: '/account-home/login',
      query: {redirect: to.fullPath}
    })
  } else {
    next()
  }
})

export default router



