<template>
  <aside class="answer-card" :style="{transform:transformX}">
    <el-card>
      <div slot="header">
        <span>答题卡<small style="color: #a3a3a1;font-size: .7em">（双击标记题目,每部分均可展开）</small></span>
        <i @click="showCard = !showCard"
           :class="[showCard ? 'el-icon-d-arrow-left' : 'el-icon-d-arrow-right']"></i>
        <el-badge value="答题卡" class="tip"></el-badge>
      </div>
      <section class="questions">
        <el-collapse v-model="activeNames">
          <el-collapse-item :title="section.name" v-for="section in localSections" :key="section.id" :name="section.id"
                            :class="{'disabled-collapse':section.completed}">
            <div @click="onCheck(section.id,$event)"
                 @dblclick="onMarked($event)">
              <div class="q-no" v-for="(question,no) in section.questions" :data-no="no+1"
                   :class="{checked:curSecId===section.id&&curQNo===no+1, 'finished-answer':question.checkedAnswer}">
                {{no + 1}}
                <div class="el-icon-question" v-show="question.marked"></div>
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </section>
    </el-card>
  </aside>
</template>
<script>
export default {
  props: ['sections', 'curQuestionNo', 'section', 'activeSecId'],
  data () {
    return {
      showCard: true,
      activeNames: []
    }
  },
  computed: {
    localSections: function () {
      return this.sections
    },
    transformX: function () {
      if (this.showCard) {
        return `translateX(${11}px)`
      } else {
        return `translateX(${-260}px)`
      }
    },
    curSecId: function () {
      return this.section.id
    },
    curQNo: function () {
      return this.curQuestionNo + 1
    }
  },
  watch: {
    activeSecId: function (newVal) {
      this.activeNames = [newVal]
    },
    showCard: function (newVal) {
      this.$emit('toggleCard', newVal)
    }
  },
  methods: {
    // 事件委托以提升性能
    onMarked: function (e) {
      // dbclick no最小值为1，不会为false
      let no = e.target.dataset.no || e.target.parentNode.dataset.no
      no && this.$emit('onMarked', {no: Number(e.target.dataset.no)})
    },
    onCheck: function (checkedSecId, e) {
      // click no最小值为1，不会为false 一次dbclick会触发两次click
      if (!e.target.dataset.no) {
        return
      }
      let no = Number(e.target.dataset.no)
      if (checkedSecId === this.curSecId) { // 选中当前sec
        this.$emit('toggleQuestion', {qNo: no - 1})
      } else {
        this.$emit('toggleQuestion', {
          qNo: no - 1,
          sectionId: checkedSecId,
          oldSectionId: this.curSecId
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .answer-card {
    width: 322px;
    transform: translateX(-260px);
    transition: all 0.25s;
    .el-icon-d-arrow-right {
      float: right;
      cursor: pointer;
    }
    .el-icon-d-arrow-left {
      float: right;
      cursor: pointer;
    }
    .tip {
      position: absolute;
      right: -22px;
      top: -10px;
    }
    .questions {
      margin: 0 22px;
      height: calc(100vh - 266px);
      overflow-y: auto;
      overflow-x: hidden;
      .disabled-collapse {
        opacity: 0.4;
      }
      .q-no {
        position: relative;
        cursor: pointer;
        display: inline-block;
        width: 41px;
        height: 41px;
        text-align: center;
        line-height: 41px;
        border: 1px solid #d2eeee;
        border-radius: 22%;
        user-select: none;
      }
      .checked {
        background: #fff9b4;
      }
      .finished-answer {
        position: relative;
      }
      .finished-answer::after {
        content: '';
        border: 6px solid transparent;
        border-top-color: #909399;
        border-right-color: #909399;
        position: absolute;
        right: 0;
        border-top-right-radius: 50%;
      }
      .el-icon-question {
        top: 0;
        left: 0;
        position: absolute;
        font-size: 16px;
        color: orange;
      }
    }
  }
</style>



