<template>
  <el-dialog title="我要充值变得更强！（ 1 元 = 100 Coin）" :visible.sync="dialogFormVisible" @close="onHidden()" class="recharge">
    <section style="text-align: center;">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="99px">
        <el-form-item label="充值金额" prop="price">
          <el-input-number v-bind:value="Number(ruleForm.price)" :disabled="!!qr_code||qrLoading"
                           v-on:input="ruleForm.price = arguments[0]&&arguments[0].toFixed(2)"
                           :min="Number(0.1)" :step="Number(0.1)"
                           auto-complete="off" style="width: 60%">
          </el-input-number>
          <el-button type="success" @click="submitForm('ruleForm')">立即充值</el-button>
        </el-form-item>
      </el-form>
      <section>
        <div class="recharge-content" v-loading="qrLoading" element-loading-text="充值二维码拼命加载中"
             :style="{ height: qr_code||qrLoading?'266px':'0px'}"
             element-loading-background="rgba(0, 0, 0, 0.9)">
          <el-row :gutter="20" style="width: 100%" v-show="qr_code||qrLoading">
            <el-col :span="10" :offset="2" class="order">
              <p><span>充值金额</span><strong>￥ {{ruleForm.price}}</strong></p>
              <p><span>优惠金额</span><strong>￥ 0.00</strong></p>
              <p style="font-size: larger"><span>实际到账</span><strong>Coin {{ruleForm.price*100}}</strong></p>
            </el-col>
            <el-col :span="12">
              <div class="img-wrapper">
                <img v-bind:src="qr_code" class="image" @load="qrLoading = false">
              </div>
            </el-col>
          </el-row>
        </div>
      </section>
    </section>
    <div slot="footer">
      <el-button-group>
        <el-button @click="resetForm('ruleForm')">重新填</el-button>
        <el-button :type="trade_status" @click="onHidden()">{{trade_status==='success'?'充值成功':'回去啦'}}</el-button>
      </el-button-group>
    </div>
  </el-dialog>
</template>

<script>
import {WalletService} from '@/service/api'

export default {
  props: ['visible'],
  computed: {
    dialogFormVisible: {
      get: function () {
        return this.visible
      },
      set: function (val) {
        this.qr_code = null
        this.qrLoading = false
        this.$emit('update:visible', val)
      }
    }
  },
  data: function () {
    return {
      qrLoading: false,
      ruleForm: {
        coupon: null,
        price: 0.1
      },
      rules: {
        price: [
          {required: true, message: '请输入充值金额', trigger: 'change'}
        ]
      },
      qr_code: '',
      qr_id: '',
      checkInterval: null,
      checkNum: 0,
      trade_status: 'default'
    }
  },
  methods: {
    onHidden: function () {
      this.dialogFormVisible = false
      this.trade_status = 'default'
      this.resetForm('ruleForm')
    },
    resetForm: function (formName) {
      this.$refs[formName].resetFields()
      this.trade_status = 'default'
      this.qr_code = null
      this.qrLoading = false
    },
    submitForm: function (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        this.qrLoading = true
        WalletService.qr(this.ruleForm.price * 100)
          .then(res => {
            if (res) {
              this.qr_code = res.qr_code
              this.qr_id = res.qr_id
              // todo 定时轮询 3s 一次 后台是否支付成功
              this.checkInterval = setInterval(() => {
                this.checkPay()
                this.checkNum++
              }, 3000)
            }
          })
      })
    },
    checkPay: function () {
      if (this.checkNum > 100) {
        clearInterval(this.checkInterval)
      }
      WalletService.check(this.qr_id)
        .then(res => {
          if (res && res.pay_status === 'success') {
            this.$message.success('成功充值 ' + res.qr_price + ' Coin')
            clearInterval(this.checkInterval)
          }
        })
    }
  },
  beforeDestroy: function () {
    clearInterval(this.checkInterval)
  }
}
</script>

<style scoped lang="scss">
  .recharge {
    .el-form {
      display: inline-block;
    }
    .recharge-content {
      display: flex;
      align-items: center;
      transition: height 0.25s;
      line-height: 2em;
      font-size: 1.3em;
      margin-top: 1em;
      .img-wrapper {
        height: 15em;
        .image {
          max-height: 100%;
        }
      }
      .order {
        margin-top: 2em;
        text-align: left;
        border: 1px dashed #eeeeee;
        span {
          font-size: smaller;
        }
        strong {
          float: right;
        }
      }
    }
  }
</style>



