<template>
  <section class="suggestions-view">
    <div class="main" :style="{'height':replyVisible?'50%':'90%'}">
      <article class="content">
        <div class="header" >
          <h3>{{ticket.title?ticket.title:'无标题'}}</h3>
          <p><em>{{ticket.created_at|dateFormat('YYYY-MM-DD hh:mm:ss')}}</em></p>
        </div>
        <div v-for="content in ticket.contents" class="content-box">
          <div class="protrait" :style="content.is_admin?adminStyle:userStyle">{{content.is_admin?'管':'我'}}</div>
          <section class="conversation ol-content-box">
            <div class="word">{{content.content}}
            </div>
            <div>
              <div v-for="(url,index) in content.pics" class="img-wrapper" @click="onOpenLightbox(content.pics,index)">
                <div class="img-box">
                  <img :src="url|imgUrlFormat">
                </div>
              </div>
            </div>
            <div class="date">
              <strong>{{content.created_at|dateFormat('YYYY-MM-DD hh:mm:ss')}}</strong>
            </div>
          </section>
        </div>
      </article>
    </div>
    <div class="footer-operation" :style="{'height':replyVisible?'50%':'10%'}">
      <el-button type="primary" style="height: 100%;width: 100%;font-size: 100%" v-if="!replyVisible"
                 @click="onShowReply">继续回复
      </el-button>
      <reply v-on:on-reply="onReply"></reply>
    </div>
  </section>
</template>

<script>
import Lightbox from '@/commons/plugins/Lightbox'
import Reply from './Reply'
import {Urls, TicketService} from '@/service/api'

export default {
  components: {
    'reply': Reply
  },
  data: function () {
    return {
      adminStyle: {
        float: 'left',
        background: '#303133'
      },
      userStyle: {
        float: 'right',
        background: '#409EFF'
      },
      replyVisible: false,
      ticket: {}
    }
  },
  created: function () {
    this.getTicket()
  },
  methods: {
    getTicket: function () {
      TicketService.one(this.$route.params.id)
        .then(res => {
          if (res) {
            this.ticket = res.ticket
          }
        })
    },
    onOpenLightbox: function (urls, index) {
      let imgUrls = urls.map(
        function (val) {
          return Urls.img + val
        })
      Lightbox.open({urls: imgUrls, index: index})
    },
    onShowReply: function () {
      this.replyVisible = true
    },
    onReply: function (val) {
      if (!val) {
        this.replyVisible = false
        return
      }
      TicketService.reply(this.ticket.id, val.content, val.pics)
        .then(res => {
          if (res) {
            this.$message.success('回复成功')
            this.getTicket()
            this.replyVisible = false
          } else {
            this.$message.error('回复失败')
          }
        })
    }
  }
}
</script>

<style scoped lang="scss">
  .suggestions-view {
    height: 100%;
    box-sizing: border-box;
    .main {
      overflow-y: auto;
      height: 90%;
      transition: height 0.25s;
      .content {
        .header {
          margin: 0 5em;
          position: sticky;
          top: -1.5em;
          background: white;
          box-shadow: 0px 0px 1em #eee;
          border:1px solid #eee;
          z-index: 9;
          padding: 0.5em;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
        }
        .content-box {
          .protrait {
            width: 2em;
            height: 2em;
            line-height: 2em;
            color: white;
            text-align: center;
            border-radius: 50%;
            font-size: 2em;
          }
          .conversation {
            margin: 1em 5em;
            .word {
              padding: 1em;
              margin: 0.5em 0;
              border-radius: 5px;
              border: 1px solid #a3a3a1;
            }
            .img-wrapper {
              padding: 10%;
              position: relative;
              display: inline-block;
              .img-box {
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                box-sizing: border-box;
                position: absolute;
                border: 0.5em solid #eeeeee;
                cursor: pointer;
                overflow: hidden;
                img {
                  max-width: 100%;
                  max-height: 100%;
                  position: absolute;
                  top: 0;
                  bottom: 0;
                  left: 0;
                  right: 0;
                  margin: auto;
                }
              }
              .img-box:after {
                content: '点击预览大图';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                background: rgba(0, 0, 0, 0.6);
                color: white;
                font-size: .7em;
                height: 3em;
                line-height: 3em;
                text-align: center;
                transform: translateY(100%);
                transition: transform 0.25s;
              }
              .img-box:hover {
                border-color: #409EFF;
              }
              .img-box:hover:after {
                transform: translateY(0);
              }
            }

            .date {
              margin-top: 1em;
              text-align: right;
            }
          }
        }
      }
    }
    .footer-operation {
      margin: 1%;
      height: 8%;
      .reply {
        height: 100%;
        box-sizing: border-box;
      }
    }
  }
</style>



