<template>
  <div id="app">
    <router-view/>
  </div>
</template>
<script>
export default {
  name: 'App',
  data () {
    return {}
  }
}
</script>

<style lang="scss">
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
  }
  .link-text {
    text-decoration: none;
    color: inherit;
  }
  .ol-content-box {
    border: 1px solid #eee;
    border-radius: 3px;
    box-shadow: 0px 0px .5em #eee;
    padding: 1em 1em 1em 1em;
    background: white;
  }
  .overflow {
    overflow: hidden; /*溢出隐藏*/
    text-overflow: ellipsis; /*以省略号...显示*/
    white-space: nowrap; /*强制不换行*/
    width: 100%;
    display: block;
  }
  a {
    color: #409EFF
  }
</style>



