<template>
  <el-container class="online-exam-center">
    <el-header>
      <strong>
        Online Exam
      </strong>
      <div class="user-img"><img v-if="user.avatar" :src="user.avatar | imgUrlFormat"></div>
      <section class="user-info">
        <p>用户：<span>{{user.nickname}}</span></p>
        <p>手机：<span>{{user.phone}}</span></p>
      </section>
    </el-header>
    <el-main>
      <router-view/>
    </el-main>
  </el-container>
</template>

<script>
import { Notification } from 'element-ui'
function onbeforeunload (e) {
  e = e || window.event
  // 兼容IE8和Firefox 4之前的版本
  if (e) {
    e.returnValue = '确认离开本场考试?（非正常交卷，系统不会保存任何答案）'
  }
  // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
  return '确认离开本场考试?（非正常交卷，系统不会保存任何答案）'
}

export default {
  data () {
    return {
      user: this.$store.state.user
    }
  },
  beforeRouteEnter (to, from, next) { // TODO
    if (from.name !== 'ExamList') { // 拦截异常路径进入
      Notification.info({
        title: '提示',
        message: '您已退出考试。'
      })
      next({name: 'ExamList'})
      return
    }
    window.addEventListener('beforeunload', onbeforeunload)
    next()
  },
  beforeRouteLeave (to, from, next) { // 防止意外离开
    if (to.name === 'CompletedExamView') {
      window.removeEventListener('beforeunload', onbeforeunload)
      next()
      return
    }
    const answer = window.confirm('确认离开本场考试?（非正常交卷，系统不会保存任何答案）')
    if (answer) {
      window.removeEventListener('beforeunload', onbeforeunload)
      Notification.info({
        title: '提示',
        message: '您已退出考试。'
      })
      next()
    } else {
      next(false)
    }
  }
}
</script>

<style scoped lang="scss">
  .online-exam-center {
    .el-header {
      position: relative;
      display: flex;
      background-color: #545c64;
      color: white;
      line-height: 60px;
      .user-img {
        display: inline-block;
        height: 58px;
        width: 58px;
        margin-left: 11px;
        border: 1px dashed #ddd;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .user-info {
        line-height: 30px;
        margin-left: 11px;
        p {
          margin: 0;
        }
      }
      .exam-name {
        margin-left: 11px;
      }
      .close-btn {
        position: absolute;
        right: 11px;
        top: 0;
        bottom: 0;
      }
    }
    .el-main {
      overflow-y: auto;
      height: calc(100vh - 80px);
    }
  }
</style>



