<template>
  <div>
    <h3>
      购买会员等级
    </h3>
    <section class="recharge">
      <div v-for="product in products" class="member">
        <div class="ol-content-box">
          <section style="text-align: center;">
            <strong style="color:#606266">{{product.goods_name}}</strong>
            <h2><span class="bigger-font">{{product.price}}</span> Coin</h2>
            <em style="color:#67C23A"><span class="bigger-font">{{product.describe}}</span></em>
          </section>
          <div class="bottom clearfix">
            <el-button type="success" @click="purchase(product)" round class="button">買買買！</el-button>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>

import {WalletService} from '@/service/api'

export default {
  name: 'recharge',
  data () {
    return {
      products: {}
    }
  },
  mounted: function () {
    this.listProduct()
  },
  computed: {
    wallet: function () {
      return this.$store.state.wallet
    }
  },
  methods: {
    listProduct: function () {
      WalletService.pageProd(1, 6)
        .then(res => {
          if (res) {
            this.products = res.products
          }
        })
    },
    purchase: function (product) {
      this.$confirm(`是否购买 ${product.goods_name} ${product.describe}?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'info'
      }).then(() => {
        if (this.wallet.coin < product.price) {
          this.$message.error('对不起，您的 Coin 不足')
          return
        }
        WalletService.purchase(product.id)
          .then(res => {
            if (res) {
              this.$message.success('恭喜，购买成功')
            }
          })
      }).catch(() => {
      })
    }
  }

}
</script>

<style scoped lang="scss">
  .recharge {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .member {
      width: 222px;
      margin: 1em;
      .ol-content-box {
        box-shadow: 0 0 0;
        border-color: #eeeeee;

        .bottom {
          margin-top: 1em;
          border-top: 1px solid #eeeeee;
          padding-top: 1em;
          line-height: 1em;
        }
        .button {
          width: 100%;
        }
        .bigger-font {
          font-size: larger;
        }
      }
    }

  }
</style>



