<template>
  <section class="page-list">
    <header>
      <h3 class="title">消费记录</h3>
    </header>
    <el-table :data="tableData" stripe height="calc(100vh - 211px)">
      <el-table-column prop="id" label="流水号" width="111">
      </el-table-column>
      <el-table-column prop="goods_name" label="商品名称" width="333">
      </el-table-column>
      <el-table-column prop="price" label="消费金额(coin)" width="211">
      </el-table-column>
      <el-table-column label="消费日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.created_at * 1000 | dateFormat}}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      @current-change="queryPurchase"
      :total="total">
    </el-pagination>
  </section>
</template>

<script>
import {WalletService} from '@/service/api'

export default {
  data () {
    return {
      tableData: [],
      total: 0
    }
  },
  mounted: function () {
    this.queryPurchase(1)
  },
  methods: {
    queryPurchase: function (val) {
      WalletService.pagePurc(1, 10)
        .then(res => {
          if (res) {
            this.tableData = res.purchases
            this.total = res.total
          } else {
            this.tableData = []
            this.total = 0
          }
        })
    }
  }
}
</script>

<style scoped lang="scss">
  .page-list {
    .title {
      margin-right: 2em;
      display: inline-block;
    }
  }
</style>



