<template>
  <el-dialog title="修改我的信息" :visible.sync="dialogFormVisible" @close="onHidden()" class="recharge">
    <div class="user-info-edit">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px">
        <el-form-item label="性别" prop="gender">
          <el-select v-model="ruleForm.gender" placeholder="请选择性别">
            <el-option
              v-for="item in genders"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="昵称" prop="nickname">
          <el-input v-model="ruleForm.nickname"></el-input>
        </el-form-item>
        <el-form-item label="省份" prop="province">
          <el-select v-model="ruleForm.province" placeholder="请选择省份">
            <el-option
              v-for="item in provinces"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="生日" prop="birthday">
          <el-date-picker type="date" placeholder="选择生日" v-model="ruleForm.birthday"
                          value-format="timestamp"></el-date-picker>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input v-model="ruleForm.address"></el-input>
        </el-form-item>
        <el-form-item label="头像">
          <el-upload class="avatar-uploader" ref="upload"
                     action="http://upload-z2.qiniup.com"
                     :show-file-list="false"
                     :data="token"
                     :on-success="handleAvatarSuccess"
                     :before-upload="beforeUpload">
            <div v-if="ruleForm.avatar" class="avatar">
              <img :src="ruleForm.avatar | imgUrlFormat">
              <i class="el-icon-error upload-delete" @click.stop="clearUpload"></i>
              <div class="edit">
                <i class="el-icon-edit-outline"></i>
              </div>
            </div>
            <i class="el-icon-plus avatar-uploader-icon" v-else></i>
          </el-upload>
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer">
      <el-button-group>
        <el-button @click="onHidden">不想改了</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">立即修改</el-button>
      </el-button-group>
    </div>
  </el-dialog>
</template>

<script>
import {SecurityService, UserService} from '@/service/api'
import {Code} from '@/commons/code'

const genders = [{
  value: Code.Unknow,
  label: Code.gender(Code.Unknow)
}, {
  value: Code.Man,
  label: Code.gender(Code.Man)
}, {
  value: Code.Woman,
  label: Code.gender(Code.Woman)
}]

const provinces = ['保密', '北京', '天津', '上海', '重庆', '河北', '山西', '辽宁', '吉林', '黑龙江',
  '江苏', '浙江', '安徽', '福建', '江西', '山东', '河南', '湖北', '湖南', '广东', '海南', '四川', '贵州', '云南', '陕西', '甘肃', '青海', '内蒙古', '广西', '西藏', '宁夏', '新疆维吾尔自治区', '香港', '澳门', '台湾']

const Unkonw = '保密'
export default {
  props: ['visible'],
  computed: {
    dialogFormVisible: {
      get: function () {
        return this.visible
      },
      set: function (val) {
        this.$emit('update:visible', val)
      }
    }
  },
  watch: {
    visible: function (newVal) {
      this.$refs['ruleForm'] && this.$refs['ruleForm'].resetFields()
      if (newVal) {
        let user = JSON.parse(JSON.stringify(this.$store.state.user))
        user.birthday && (user.birthday *= 1000)
        this.ruleForm = user
      }
    }
  },
  data () {
    return {
      genders: genders,
      provinces: provinces,
      ruleForm: {
        nickname: null,
        gender: null,
        province: null,
        birthday: null,
        address: null,
        avatar: null
      },
      rules: {
        nickname: [
          {max: 16, message: '长度不能超过 16 个字符', trigger: 'change'}
        ],
        address: [
          {max: 16, message: '长度不能超过 16 个字符', trigger: 'change'}
        ]
      },
      token: null
    }
  },
  methods: {
    clearUpload: function () {
      this.$refs['upload'].clearFiles()
      this.ruleForm.avatar = null
    },
    onHidden: function () {
      this.dialogFormVisible = false
    },
    handleAvatarSuccess: function (i) {
      this.ruleForm.avatar = i.key
    },
    beforeUpload: function () {
      if (this.token) {
        this.token.resource_key = 'user/' + new Date() + '.jpg'
        return true
      }
      const self = this
      return new Promise(function (resolve, reject) {
        SecurityService.qiniuToken()
          .then(res => {
            if (res) {
              self.token = {
                token: res.qiniu,
                resource_key: 'user/' + new Date() + '.jpg'
              }
              resolve()
            } else {
              self.$message.error('上传令牌获取失败')
              reject(res)
            }
          })
      })
    },
    submitForm: function (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        let birthday = 0
        this.ruleForm.birthday && (birthday = (this.ruleForm.birthday / 1000))
        let avatar = Unkonw
        this.ruleForm.avatar && (avatar = this.ruleForm.avatar)
        let user = {
          nickname: this.ruleForm.nickname || Unkonw,
          gender: this.ruleForm.gender || 0,
          province: this.ruleForm.province || Unkonw,
          birthday: birthday,
          address: this.ruleForm.address || Unkonw,
          avatar: avatar
        }
        UserService.infoChange(user)
          .then(res => {
            if (res) {
              this.$message.success('修改成功')
              this.onHidden()
              UserService.me()
            }
          })
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .user-info-edit {
    text-align: center;
    .el-form {
      display: inline-block;
      text-align: left;
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
      }
      .avatar-uploader-icon:hover {
        border: 1px dashed #409EFF;
      }
      .avatar {
        width: 178px;
        height: 178px;
        position: relative;
        .upload-delete {
          position: absolute;
          right: -1.1em;
          top: 0;
          color: red;
          font-size: 2em;
          opacity: 1;
        }
        img {
          border-radius: 6px;
          width: 100%;
          height: 100%;
        }
        .edit {
          cursor: pointer;
          z-index: 1;
          position: absolute;
          width: 100%;
          height: 100%;
          line-height: 178px;
          left: 0;
          top: 0;
          cursor: pointer;
          text-align: center;
          color: #fff;
          opacity: 0;
          font-size: 20px;
          background-color: rgba(0, 0, 0, .5);
          -webkit-transition: opacity .3s;
          transition: opacity .3s;
        }
        .edit:hover {
          opacity: 1;
        }
      }
    }
  }
</style>



