<template>
  <section class="account-login">
    <h1 class="online-exam-title">Welcome to Online Exam</h1>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="55px" class="login-form">
      <el-form-item label="账户" prop="account">
        <el-input type="text" v-model="ruleForm.account" auto-complete="off"/>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password" auto-complete="off"/>
      </el-form-item>
      <el-form-item>
        <div ref="captcha" style="height: 44px">
          <div class="l-captcha" data-site-key="f62d0270ebb875b67ef738d9dd3e2deb" data-width="999"></div>
        </div>
      </el-form-item>
      <el-form-item style="text-align: left">
        <el-switch v-model="ruleForm.remember" active-text="下次自动登录">
        </el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')" style="width: 100%;">登录</el-button>
        <div class="register">
          <router-link to="/account-home/register">没有账号？注册一个</router-link>
        </div>
      </el-form-item>
    </el-form>
  </section>
</template>
<script>
import * as types from '@/service/types'
import {load} from '@/commons/script-loader'
import {AccountService, Urls} from '@/service/api'

export default {
  data () {
    let validateAccount = (rule, value, callback) => {
      let phone = 11 && /^((13|14|15|17|18)[0-9]{9})$/i
      let email = /^([\w-_]+(?:\.[\w-_]+)*)@((?:[a-z0-9]+(?:-[a-zA-Z0-9]+)*)+\.[a-z]{2,6})$/i
      let username = /^[\w][\w-_]{5,31}$/i
      if (phone.test(value)) {
        this.ruleForm.phone = value
      } else if (email.test(value)) {
        this.ruleForm.email = value
      } else if (username.test(value)) {
        this.ruleForm.username = value
      } else {
        callback(new Error('请输入合法账户：手机，邮箱，用户名'))
        this.resetForm()
      }
      callback()
    }
    return {
      loading: null,
      ruleForm: {
        email: '',
        phone: '',
        username: '',
        account: '',
        password: '',
        remember: false,
        captcha: ''
      },
      rules: {
        account: [
          {required: true, message: '请输入账户名称', trigger: 'change'},
          {validator: validateAccount, trigger: 'change'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'change'},
          {min: 6, max: 32, message: '长度在 6 到 32 个字符', trigger: 'change'}
        ]
      }
    }
  },
  mounted: function () {
    this.$store.commit(types.TITLE, 'Login')
    this.loading = this.$loading({
      target: this.$refs.captcha,
      spinner: 'el-icon-loading',
      background: 'rgba(155, 155, 155, 0.3)'
    })
    load(this.$el, Urls.captcha, this.captchaCallback)
  },
  methods: {
    captchaCallback: function () {
      this.loading.close()
    },
    resetForm: function () {
      // captcha code reset, do not delete
      // eslint-disable-next-line no-undef
      LUOCAPTCHA.reset()
      for (let key in this.ruleForm) {
        if (key === 'account') {
          return
        }
        if (key === 'remember') {
          this.ruleForm[key] = false
        }
        this.ruleForm[key] = ''
      }
    },
    submitForm: function (formName) {
      // todo 人机验证 开启 去掉下面的注释即可
      // let captchaInputBox = document.querySelector('#lc-captcha-response')
      // if (!captchaInputBox || !captchaInputBox.value) {
      //   this.$message.error('请通过人机验证')
      //   return
      // }
      // this.ruleForm.captcha = captchaInputBox.value
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        AccountService.login(this.ruleForm).then(
          res => {
            if (res) {
              this.$message.success('登录成功')
              if (this.$router.currentRoute.query.redirect) {
                this.$router.push({path: this.$router.currentRoute.query.redirect})
              } else {
                this.$router.push({path: '/user-center/exam-list'})
              }
            } else {
              this.resetForm()
            }
          })
      })
    }
  }
}

