<template>
  <section class="account-reg">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="111px" class="reg-form">
      <el-form-item label="手机" prop="phone">
        <el-input placeholder="必填" type="text" v-model="ruleForm.phone" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="验证码" prop="phone_code">
        <el-input placeholder="必填" v-model="ruleForm.phone_code" class="input-with-select">
          <el-button slot="append" @click="getPhoneCode('ruleForm')">{{time?`${time}秒后再次发送`:'获取验证码'}}</el-button>
        </el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="username">
        <el-input placeholder="必填" type="text" v-model="ruleForm.username" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input placeholder="必填" type="text" v-model="ruleForm.email" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input placeholder="必填" type="password" v-model="ruleForm.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="pass_repeat">
        <el-input placeholder="必填" type="password" v-model="ruleForm.pass_repeat" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="邀请码" prop="invite_code">
        <el-input placeholder="选填，注册奖励" type="text" v-model="ruleForm.invite_code" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')" style="width: 100%;">注册</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import {AccountService, SecurityService} from '@/service/api'

export default {
  data () {
    let validatePassRepeat = (rule, value, callback) => {
      if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    let validatePhone = (rule, value, callback) => {
      let reg = 11 && /^((13|14|15|17|18)[0-9]{9})$/
      if (!reg.test(value)) {
        callback(new Error('请输入合法的手机号'))
      } else {
        callback()
      }
    }
    return {
      time: 0,
      ruleForm: {
        phone: '',
        phone_code: '',
        username: '',
        email: '',
        pass_repeat: '',
        password: '',
        invite_code: ''
      },
      rules: {
        phone: [
          {required: true, message: '请输入手机号', trigger: 'change'},
          {validator: validatePhone, trigger: 'change'}
        ],
        phone_code: [
          {required: true, message: '请输入手机验证码', trigger: 'change'},
          {min: 6, max: 6, message: '长度在 6 个字符', trigger: 'change'}
        ],
        username: [
          {required: true, message: '请输入账号', trigger: 'change'},
          {min: 6, max: 32, message: '长度在 6 到 32 个字符', trigger: 'change'}
        ],
        email: [
          {required: true, message: '请输入邮箱', trigger: 'change'},
          {min: 6, max: 64, message: '长度在 6 到 64 个字符', trigger: 'change'},
          {type: 'email', message: '邮箱格式不合法', trigger: 'change'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'change'},
          {min: 6, max: 32, message: '长度在 6 到 32 个字符', trigger: 'change'}
        ],
        pass_repeat: [
          {required: true, message: '请输入密码', trigger: 'change'},
          {validator: validatePassRepeat, trigger: 'change'}
        ]
      }
    }
  },
  methods: {
    countDown: function () {
      let self = this
      setTimeout(function () {
        self.time -= 1
        if (self.time) {
          self.countDown()
        }
      }, 999)
    },
    getPhoneCode: function () {
      if (this.time) {
        return
      }
      this.$refs['ruleForm'].validateField('phone', (valid) => {
        if (valid) {
          return false
        }
        SecurityService.sms(this.ruleForm.phone)
          .then(res => {
            if (res) {
              this.time = 60
              this.countDown()
              this.$message.success('验证码发送成功')
            } else {
              this.$message.error('验证码发送失败')
            }
          })
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        AccountService.register(this.ruleForm).then(
          res => {
            if (res) {
              this.$message.success('注册成功')
              if (this.$router.currentRoute.query.redirect) {
                this.$router.push({path: this.$router.currentRoute.query.redirect})
              } else {
                this.$router.push({path: '/user-center/exam-list'})
              }
            }
          })
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .account-reg {
    .reg-form {
      max-width: 366px;
      margin: 0 auto;
    }
  }
</style>



