export class AccountService {
  static login (auth) {
    return axios.post('login', auth)
      .then(res => {
        if (res.data) {
          loginCommit(res.data)
        }
        return Promise.resolve(res.data)
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static register (auth) {
    return axios.post('register', auth)
      .then(res => {
        if (res.data) {
          loginCommit(res.data)
          UserService.me()
        }
        return Promise.resolve(res.data)
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static logout () {
    return axios.get('token/logout')
      .then(res => {
        store.commit(types.LOGOUT)
        return Promise.resolve(res.data)
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static resetPass (auth) {
    return axios.post('reset/pass', auth)
      .then(res => {
        console.log(res)
        res.data === 'success' && store.commit(types.LOGOUT)
        return Promise.resolve(res.data)
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static refresh () {
    if (store.state.exp - new Date().getTime() > 3 * 3600 * 1000) {
      return
    }
    return axios.get('token/refresh')
      .then(res => {
        if (res.data) {
          loginCommit(res.data)
        }
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static refreshAnyway () {
    return axios.get('token/refresh')
      .then(res => {
        if (res.data) {
          loginCommit(res.data)
        }
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }
}

function loginCommit (auth) {
  store.commit(types.LOGIN, auth.jwt)
  store.commit(types.EXP, auth.exp)
}

export class UserService {
  static me () {
    return axios.get('user/me')
      .then(res => {
        if (res.data) {
          let u = res.data.user
          let i = res.data.user_info
          let user = {
            uid: u.id,
            username: u.username,
            email: u.email,
            phone: u.phone,
            level: i.level,
            nickname: i.nickname,
            address: i.address,
            avatar: i.avatar === '保密' ? null : i.avatar,
            gender: i.gender,
            province: i.province,
            birthday: i.birthday > 0 ? i.birthday : null
          }
          store.commit(types.USER, user)
        }
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static infoChange (info) {
    return axios.post('user/info/change', info)
      .then(res => {
        return Promise.resolve(res.data)
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static inviteCreate () {
    return axios.get('user/invite/create')
      .then(res => {
        return Promise.resolve(res.data)
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static invitePage (num, size) {
    return axios.post('user/invite/page', {
      page_num: parseInt(num),
      page_size: parseInt(size)
    }).then(res => {
      return Promise.resolve(res.data)
    }).catch(
      error => {
        Message.error(ServerError.Translate(error))
      }
    )
  }
}

export class WalletService {
  static me () {
    return axios.get('wallet/me')
      .then(res => {
        if (res.data) {
          let w = res.data.wallet
          let wallet = {
            coin: w.coin ? w.coin : 0,
            coin_frozen: w.coin_frozen ? w.coin_frozen : 0
          }
          store.commit(types.WALLET, wallet)
        }
      }).catch(
        error => {
          Message.error(ServerError.Translate(error))
        }
      )
  }

  static qr (price) {
    return axios.post('wallet/qr', {
      qr_name: 'OES Coin:' + store.state.user.username + ':' + store.state.user.uid + ':' + price,
      qr_price: parseInt(price)
    }).then(res => {
      return Promise.resolve(res.data)
    }).catch(
      error => {
        Message.error(ServerError.Translate(error))
      }
    )
  }

  static check (qrId) {
    return axios.post('wallet/check/pay', {
      qr_id: qrId.toString()
    }).then(res => {
      if (res.data && res.data.pay_status === 'success') {
        WalletService.me()
      }
      return Promise.resolve(res.data)
    }).catch(
      error => {
        Message.error(ServerError.Translate(error))
      }
    )
  }

  static pageProd (num, size) {
    return axios.post('wallet/page/product', {
      page_num: parseInt(num),
      page_size: parseInt(size)
    }).then(res => {
      return Promise.resolve(res.data)
    }).catch(
      error => {
        Message.error(ServerError.Translate(error))
      }
    )
  }

  static pagePay (num, size) {
    return axios.post('wallet/page/payment', {
      page_num: parseInt(num),
      page_size: parseInt(size),
      desc: true
    }).then(res => {
      return Promise.resolve(res.data)
    }).catch(
      error => {
        Message.error(ServerError.Translate(error))
      }
    )
  }

  static pagePurc (num, size) {
    return axios.post('wallet/page/purchase', {
      page_num: parseInt(num),
      page_size: parseInt(size),
      desc: true
    }).then(res => {
      return Promise.resolve(res.data)
    }).catch(
      error => {
        Message.error(ServerError.Translate(error))
      }
    )
  }

  static purchase (id) {
    return axios.post('wallet/purchase', {
      prod_id: parseInt(id)
    }).then(res => {
      if (res.data && res.data.jwt) {
        loginCommit(res.data)
        UserService.me()
        WalletService.me()
      }
      return Promise.resolve(res.data)
    }).catch(
      error => {
        Message.error(ServerError.Translate(error))
      }
    )
  }
}

