<template>
  <div class="online-exam-pre">
    <count-down class="count-down" :timer="time">
      <strong>
        倒计时
      </strong>
    </count-down>
    <div class="container">
      <section class="main" v-once>
        <div class="header ol-content-box">
          <section>
            <h2>“知觉速度与准确性”说明</h2>
            <article>
              <p>本部分共60题，时限10分钟，其中阅读说明2分钟，作答8分钟。此部分固定在第一部分作答，不允许先作答其他部分。阅读说明满2分钟后，可开始答题，作答满8分钟后方能选择其他部分继续作答。</p>
              <p>本部分每道试题分值相同，采用倒扣分的方式计分，扣完本部分总分为止。答对则得分，答错倒扣分，不答不得分。</p>
              <p>本部分包括三种类型的题目：</p>
            </article>
          </section>
        </div>
        <div class="ol-content-box">
          <section>
            <h3>第一种类型的题目：定位数字区间</h3>
            <article>
              <p>给定一个包含有一些数字区间的表格，让你确定某一给定的数字属于表格中的哪一数字区间，这一区间的标号就是正确答案。</p>
              <p>例题：<img src="../../../../assets/q1.png">
              </p>
              <p>例题1．6623</p>
              <p>A．① B．② C．③ D．④ E．⑤</p>
              <p>A．① B．② C．③ D．④ E．⑤</p>
              <p>解答：</p>
              <p>题1的数字在6448~6721之间，所在区间的标号为⑤，故答案为E；</p>
              <p>题2的数字在4921~5636之间，所在区间的标号为②，故答案为B。</p>
            </article>
          </section>
        </div>
        <div class="ol-content-box">
          <section>
            <h3>第二种类型的题目：寻找相同字符数量</h3>
            <article>
              <p>给定一个词表，要求你将每道题所给的五个词与词表中的词相对照，找出包含在词表中词的个数，如果所给的词没有一个包含在词表中，答案即为0。</p>
              <p>例题：<img src="../../../../assets/q2.png"></p>
              <p>例题3．明晰 供养 功德 割爱 违犯</p>
              <p>
                A．0
                B．1
                C．2
                D．3
                E．4
              </p>
              <p>
                A．0
                B．1
                C．2
                D．3
                E．4
              </p>
              <p>解答：</p>
              <p>题3中“明晰”、“功德”包含在词表中，故包含在词表中词的个数为2，答案为C；</p>
              <p>题4中所给的词没有一个包含在词表中，故答案为A。</p>
            </article>
          </section>
        </div>
        <div class="ol-content-box">
          <section>
            <h3>第三种类型的题目：字符替换核对</h3>
            <article>
              <p>给定一个数字或字母与汉字的对应表，要求你判断题目中用数字或字母替换汉字时，哪一个选项替换得完全正确。</p>
              <p>例题：<img src="../../../../assets/q3.png"></p>
              <p>例题5：立川手平</p>
              <p>
                A．LQ74
                B．76QP
                C．L67P
                D．L69P
                E．L67Q
              </p>
              <p>例题6：太州木大</p>
              <p>
                A．Q607
                B．940Q
                C．9607
                D．96Q7
                E．049Q
              </p>
              <p>解答：</p>
              <p>题5中与“立川手平”对应的数字或字母是“L67P”，故答案为C；</p>
              <p>题6中与“太州木大”对应的数字或字母是“940Q”，故答案为A。</p>
            </article>
          </section>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import CountDown from './CountDown'
import Timer from '@/commons/count-down-timer'
import {AccountService} from '@/service/api'

export default {
  data () {
    return {
      allTimerId: null,
      time: {
        hour: 'x',
        min: 'xx',
        second: 'xx'
      }
    }
  },
  created: function () {
    AccountService.refresh()
    let allTimer = {
      // todo 上线后 时间需要修改到120s
      offsetSecond: 12,
      callback: this.onAllTimeChanged,
      endCallback: this.onAllTimerEnd
    }
    this.allTimerId = Timer.add(allTimer)
    Timer.startCountDown()
  },
  destroyed: function () {
    Timer.remove(this.allTimerId)
  },
  components: {
    'count-down': CountDown
  },
  methods: {
    onAllTimerEnd: function () {
      this.$router.replace({
        path: `/exam-center/${this.$route.params.id}/exam-room`
      })
    },
    onAllTimeChanged: function (timing) {
      this.time = timing
    }
  }
}
</script>

<style scoped lang="scss">
  .online-exam-pre {
    height: 100%;
    overflow: hidden;
    position: relative;
    .container{
      height: 100%;
      overflow-y: auto;
      .main {
        margin: 0 222px;
        .header {
          position: sticky;
          margin: 0 -22px;
          top: -20px;
        }
        .text-align-center {
          text-align: center;
        }
      }
    }
    .count-down {
      position: absolute;
    }
  }
</style>



