<template>
  <el-dialog class="modify-password" title="修改密码" :visible.sync="dialogFormVisible" width="455px">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="99px">
      <el-form-item label="手机" prop="phone">
        <el-input type="text" v-model="ruleForm.phone" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="验证码" prop="phone_code">
        <el-input placeholder="必填" v-model="ruleForm.phone_code" class="input-with-select">
          <el-button slot="append" @click="getPhoneCode('ruleForm')">{{time?`${time}秒后再次发送`:'获取验证码'}}</el-button>
        </el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="pass_repeat">
        <el-input type="password" v-model="ruleForm.pass_repeat" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="onHidden()">取 消</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import {AccountService, SecurityService} from '@/service/api'

export default {
  name: 'ModifyPassword',
  props: ['visible'],
  computed: {
    dialogFormVisible: {
      get: function () {
        return this.visible
      },
      set: function (val) {
        this.$emit('update:visible', val)
      }
    }
  },
  data () {
    let validatePassRepeat = (rule, value, callback) => {
      if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    let validatePhone = (rule, value, callback) => {
      let reg = 11 && /^((13|14|15|17|18)[0-9]{9})$/
      if (!reg.test(value)) {
        callback(new Error('请输入合法的手机号'))
      } else {
        callback()
      }
    }
    return {
      time: 0,
      ruleForm: {
        password: '',
        pass_repeat: '',
        phone: '',
        phone_code: ''
      },
      rules: {
        password: [
          {required: true, message: '请输入密码', trigger: 'change'},
          {min: 6, max: 32, message: '长度在 6 到 32 个字符', trigger: 'change'}
        ],
        pass_repeat: [
          {required: true, message: '请输入密码', trigger: 'change'},
          {validator: validatePassRepeat, trigger: 'change'}
        ],
        phone: [
          {required: true, message: '请输入手机号', trigger: 'change'},
          {validator: validatePhone, trigger: 'change'}
        ],
        phone_code: [
          {required: true, message: '请输入手机验证码', trigger: 'change'},
          {min: 6, max: 6, message: '长度在 6 个字符', trigger: 'change'}
        ]
      }
    }
  },
  methods: {
    getPhoneCode: function () {
      if (this.time) {
        return
      }
      this.$refs['ruleForm'].validateField('phone', (valid) => {
        if (valid) {
          return false
        }
        SecurityService.sms(this.ruleForm.phone)
          .then(res => {
            if (res) {
              this.time = 60
              this.countDown()
              this.$message.success('验证码发送成功')
            } else {
              this.$message.error('验证码发送失败')
            }
          })
      })
    },
    countDown: function () {
      let self = this
      setTimeout(function () {
        self.time -= 1
        if (self.time) {
          self.countDown()
        }
      }, 999)
    },
    onHidden: function () {
      this.dialogFormVisible = false
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        AccountService.resetPass(this.ruleForm)
          .then(res => {
            if (res) {
              // 修改成功，清理token,自动跳转到登录页面
              this.$message.success('修改成功')
              this.$router.push({path: '/account-home/login'})
            }
          })
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .modify-password {
  }
</style>



