<template>
  <el-container class="compelted-exam-view">
    <el-header>
      <strong>
        <router-link to="/user-center/exam-list" class="link-text">Online Exam</router-link>
      </strong>
      <div class="user-img"><img v-if="user.avatar" :src="user.avatar | imgUrlFormat"></div>
      <section class="info-box">
        <div class="left">
          <p>姓名：</p>
          <p>手机：</p>
        </div>
        <div>
          <p><strong>{{user.nickname}}</strong></p>
          <p><strong>{{user.phone}}</strong></p>
        </div>
      </section>
      <section>
        <div style="margin: 0px 2em;font-size: 1.3em;">场次：{{paper.name}}</div>
      </section>
      <section class="info-box">
        <div class="left">
          <p>满分：</p>
          <p>得分：</p>
        </div>
        <div>
          <p><strong>{{paper.total_score}}</strong></p>
          <p><strong>{{paper.score}}</strong></p>
        </div>
      </section>
      <section class="info-box">
        <div class="left">
          <p>考试时间：</p>
          <p>实际耗时：</p>
        </div>
        <div>
          <p><strong>{{paper.duration}} 分钟</strong></p>
          <p><strong>{{paper.realtime}} 分钟</strong></p>
        </div>
      </section>
    </el-header>
    <el-main>
      <el-menu
        :default-active="String(curSectionId)"
        style="width: 266px;position: absolute;"
        class="el-menu-vertical-demo"
        @select="onSelectSec">
        <el-menu-item :index="String(sec.id)" v-for="sec in paper.sections" :key="sec.id">
          <span slot="title">{{sec.name}}</span>
        </el-menu-item>
      </el-menu>
      <main class="main-content" ref="questionsWrapper">
        <div class="ol-content-box" v-for="(q,index) in section.questions" :name="'qid'+q.id">
          <div slot="header" class="header">
            <i :class="[q.isCorrected ? 'el-icon-success' : 'el-icon-error']"></i><strong>{{index + 1 + '、'}}</strong>
            <div v-if="q.pic" class="img-wrapper">
              <img :src="q.pic|imgUrlFormat" class="img">
            </div>
            <strong>{{q.stem}}</strong>
          </div>
          <section class="selections">
            <p v-for="(val,key) in q.selections" :data-background="key === q.answer?'#67C23A':'red'"
               :class="{'your-answer': key == q.userAnswer,'right-answer':key == q.answer}">
              {{key}} :
              <span v-show="!val.endsWith('.jpg')">
                {{val}}
              </span>
              <img v-if="val.endsWith('.jpg')" :src="val" style="max-width: 100%;">
            </p>
            <el-alert
              v-show="!q.userAnswer"
              title="该题未选择答案"
              type="error"
              center
              show-icon
              :closable="false">
            </el-alert>
            <p class="analysis">
              <span class="analysis-hover">
              查看解析<i class="el-icon-caret-bottom"></i><br>
                <div class="analysis-content">{{q.explain}}</div>
              </span>
            </p>
          </section>
        </div>
      </main>
      <answer-card
        :questions="section.questions" :title="section.name"
        v-on:noChanged="onChangeQid"></answer-card>
    </el-main>
  </el-container>
</template>

<script>
import AnswerCard from './CompletedAnswerCard'
import {ExamService, PaperService} from '@/service/api'

export default {
  data () {
    return {
      paper: {},
      section: {},
      curSectionId: null,
      userAnswerMap: new Map()
    }
  },
  computed: {
    user: function () {
      return this.$store.state.user
    }
  },
  mounted: function () {
    ExamService.one(this.$route.params.id)
      .then(res => {
        if (res) {
          let answerMap = new Map()
          if (res.exam.user_answers) {
            for (let qid of Object.keys(res.exam.user_answers)) {
              answerMap.set(String(qid), res.exam.user_answers[qid])
            }
          }
          this.userAnswerMap = answerMap
          PaperService.one(res.exam.pid)
            .then(resPaper => {
              if (resPaper) {
                resPaper.paper.realtime = res.exam.duration
                resPaper.paper.score = res.exam.score || 0
                resPaper.paper.created = res.exam.created_at
                this.paper = resPaper.paper

                this.sections = resPaper.paper.sections
                this.curSectionId = resPaper.paper.sections[0].id
              } else {
                this.$message.error('获取试卷失败了哦，' + resPaper)
              }
            })
        }
      })
  },
  components: {
    'answer-card': AnswerCard
  },
  watch: {
    // 如果 `curSectionId` 发生改变，这个函数就会运行
    curSectionId: function (newSectionId, oldSectionId) {
      if (this.paper.sections) {
        for (let sec of this.paper.sections) {
          if (String(sec.id) === String(newSectionId)) {
            this.section = sec
            break
          }
        }
      }
      if (this.section.questions) {
        for (let q of this.section.questions) {
          let a = this.userAnswerMap.get(String(q.id))
          q.isCorrected = a && (q.answer === a)
          q.userAnswer = a || null
        }
      }

      if (this.$refs.questionsWrapper) {
        this.$refs.questionsWrapper.scrollTop = 0
      }
    }
  },
  methods: {
    onSelectSec: function (key) {
      this.curSectionId = key
    },
    onChangeQid: function (qidObj) {
      if (qidObj && qidObj.qid) {
        let qNode = this.$refs.questionsWrapper.querySelector(`.ol-content-box[name=qid${qidObj.qid}]`)
        if (qNode) {
          qNode.scrollIntoView()
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .compelted-exam-view {
    .el-header {
      position: relative;
      display: flex;
      background-color: #545c64;
      color: white;
      line-height: 60px;
      .user-img {
        display: inline-block;
        height: 58px;
        width: 58px;
        margin-left: 11px;
        border: 1px dashed #ddd;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .info-box {
        line-height: 30px;
        margin-left: 1em;
        .left {
          text-align: right;
        }
        div {
          display: inline-block;
          p {
            margin: 0;
          }
        }
      }
      .exam-name {
        margin-left: 11px;
      }
      .close-btn {
        position: absolute;
        right: 11px;
        top: 0;
        bottom: 0;
      }
    }
    .el-main {
      position: relative;
      .answer-card {
        width: 322px;
        top: 20px;
        position: absolute;
        right: 8px;
      }
      .main-content {
        position: relative;
        margin-left: 299px;
        margin-right: 333px;
        height: calc(100vh - 100px);
        overflow-y: auto;
        .ol-content-box {
          position: relative;
          overflow: initial;
          margin-top: 0.5em;
          .header {
            margin: 0 4em;
            .img-wrapper {
              text-align: center;
              .img {
                max-width: 100%;
              }
            }
          }
          .el-icon-success {
            color: springgreen;
            position: absolute;
            font-size: 2em;
            left: 0.5em;
            top: 0.25em;
          }
          .el-icon-error {
            color: red;
            position: absolute;
            font-size: 2em;
            left: 0.5em;
            top: 0.3em;
          }
          .selections {
            margin: 0 4em;
            .your-answer {
              position: relative;
              color: red;
            }
            .your-answer::before {
              content: '你的答案';
              position: absolute;
              left: -5.5em;
              vertical-align: text-top;
              color: white;
              background: red;
              border-radius: 5px 0 0 5px;
              padding-left: 0.5em;
              padding-right: 0.1em;
              font-size: 0.9em;

            }
            .your-answer::after {
              content: '';
              position: absolute;
              left: -0.8em;
              top: 0;
              border: 0.5em solid transparent;
              border-left-color: red;
            }
            .right-answer {
              color: #67C23A !important;
              font-weight: bolder;
            }
            .right-answer::before {
              background: #67C23A !important;
            }
            .right-answer::after {
              border-left-color: #67C23A !important;
            }
            .analysis {
              text-align: center;
              .analysis-hover {
                cursor: pointer;
                .analysis-content {
                  overflow: hidden;
                  transition: max-height 0.25s;
                  max-height: 0;
                  color: #606266;
                  text-align: left;
                }
              }
              .analysis-hover:hover {
                .analysis-content {
                  background: #F2F6FC;
                  max-height: 30em;
                }
              }
            }
          }
        }
      }
    }
  }
</style>



