<template>
  <section class="page-list">
    <header>
      <h3 class="title">充值记录</h3>
    </header>
    <el-table :data="tableData" stripe height="calc(100vh - 211px)">
      <el-table-column prop="id" label="订单号" width="333">
      </el-table-column>
      <el-table-column prop="price" label="充值数量(coin)" width="211">
      </el-table-column>
      <el-table-column prop="status" label="订单状态" width="111">
        <template slot-scope="scope">
          <el-tag :type="scope.row.status==='success'?'success':'danger'">{{scope.row.status | rechargeStatus}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="充值日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.created_at * 1000 | dateFormat}}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      @current-change="queryPurchase"
      :total="total">
    </el-pagination>
  </section>
</template>

<script>
import {WalletService} from '@/service/api'
import {Code} from '@/commons/code'
export default {
  data () {
    return {
      tableData: [],
      total: 0
    }
  },
  filters: {
    rechargeStatus: Code.rechargeStatus
  },
  mounted: function () {
    this.queryPurchase(1)
  },
  methods: {
    queryPurchase: function (val) {
      WalletService.pagePay(1, 10)
        .then(res => {
          if (res) {
            this.tableData = res.payments
            this.total = res.total
          } else {
            this.tableData = []
            this.total = 0
          }
        })
    }
  }
}
</script>

<style scoped lang="scss">
  .page-list {
    .title {
      margin-right: 2em;
      display: inline-block;
    }
  }
</style>



