<template>
  <section class="online-exam">
    <el-form :inline="true" :model="filter" class="demo-form-inline">
      <el-form-item label="试卷名称">
        <el-input v-model="filter.name" placeholder="类似于"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="queryPapers(1)">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table :data="tableData" stripe height="calc(100vh - 215px)">
      <el-table-column prop="name" label="试卷" width="300">
      </el-table-column>
      <el-table-column label="成绩" width="80">
        <template slot-scope="scope">
          <span>{{scope.row.score?scope.row.score:0}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="duration" label="用时(分钟)" width="99">
      </el-table-column>
      <el-table-column label="考试日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.created_at *1000|dateFormat('YYYY-MM-DD hh:mm:ss')}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="onStartView(scope.row)"
            type="text"
            size="small">
            查看解析<i class="el-icon-d-arrow-right"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :current-page.sync="currentPage"
      @current-change="queryPapers"
      :total="total">
    </el-pagination>
  </section>
</template>

<script>

import {ExamService} from '@/service/api'

export default {
  data () {
    return {
      filter: {
        name: null
      },
      dialogVisible: false,
      tableData: [],
      currentPage: 1,
      total: 0,
      curPaper: {}
    }
  },
  mounted: function () {
    this.queryPapers(1)
  },
  methods: {
    queryPapers: function (val) {
      let filter = {
        num: val,
        size: 10,
        name_like: this.filter.name
      }
      ExamService.page(filter)
        .then(res => {
          if (res) {
            this.tableData = res.exams
            this.total = res.total
          }
        })
    },
    onStartView: function (item) {
      this.$router.push({path: `/completed-exam-view/${item.id}`})
    }
  }
}
</script>

<style scoped lang="scss">
  .online-exam {
    .text-align {
      text-align: center;
      line-height: 3em;
    }
  }
</style>



