<template>
  <aside class="answer-card">
    <div class="ol-content-box">
      <div slot="header">
        <p>{{title}}<small>（点击题号快速跳转）</small></p>
      </div>
      <section class="questions">
        <div v-for="(val,index) in questions" class="q-no" :class="[val.isCorrected ? 'right-answer' : (val.userAnswer?'error-answer':'unfinished-answer')]"
             @click="onCheck(val.id)">
          {{index + 1}}
        </div>
      </section>
    </div>
  </aside>
</template>

<script>
export default {
  props: ['questions', 'title'],
  data () {
    return {}
  },
  computed: {
    localQuestions: function () {
      return this.questions
    }
  },
  methods: {
    onCheck: function (qid) {
      this.$emit('noChanged', {qid: qid})
    }
  }
}
</script>

<style scoped lang="scss">
  .answer-card {
    width: 322px;
    .questions {
      margin: 0 22px;
      height: calc(100vh - 266px);
      overflow-y: auto;
      overflow-x: hidden;
      .q-no {
        cursor: pointer;
        display: inline-block;
        width: 41px;
        height: 41px;
        text-align: center;
        line-height: 41px;
        border: 1px solid #eeeeee;
        border-radius: 22%;
      }
      .q-no:hover {
        background: #409eff;
        color: white;
      }
      .error-answer {
        background: red;
        color: white;
      }
      .unfinished-answer {
        background: #E6A23C;
        color: white;
      }
      .right-answer {
        position: relative;
      }
      .right-answer::after {
        content: '';
        border: 6px solid transparent;
        border-top-color: springgreen;
        border-right-color: springgreen;
        position: absolute;
        right: 0;
        border-top-right-radius: 50%;
      }
    }
  }
</style>



