<template>
  <section class="suggestion-list">
    <header>
      <h3 class="title">我的工单列表</h3>
      <el-button type="primary" round icon="el-icon-edit" @click="routeToCreate">新建工单</el-button>
    </header>
    <el-table :data="tableData" stripe height="calc(100vh - 211px)">
      <el-table-column label="标题">
        <template slot-scope="scope">
          <span>{{scope.row.title ? scope.row.title : '无标题'}}</span>
        </template>
      </el-table-column>
      <el-table-column label="类型" width="100">
        <template slot-scope="scope">
          <span>{{scope.row.module | ticketModule}}</span>
        </template>
      </el-table-column>
      <el-table-column label="状态" width="100">
        <template slot-scope="scope">
          <span>
            {{scope.row.status | ticketStatus}}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="紧急" width="80">
        <template slot-scope="scope">
          <span>
            {{scope.row.flag | ticketFlag}}
          </span>
        </template>
      </el-table-column>
      <el-table-column label="评价" width="180">
        <template slot-scope="scope">
          <span>
            <el-rate
              disabled
              v-model="scope.row.evaluate"
              show-text>
            </el-rate>
          </span>
        </template>
      </el-table-column>
      <el-table-column label="更新日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.updated_at *1000 | dateFormat('YYYY-MM-DD hh:mm:ss')}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="140">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="onView(scope.row)"
            type="text"
            size="small">
            查看<i class="el-icon-d-arrow-right"></i>
          </el-button>
          <el-button
            @click.native.prevent="onClose(scope.row.id)"
            type="text"
            size="small">
            关闭<i class="el-icon-d-arrow-right"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      @current-change="queryTickets"
      :total="total">
    </el-pagination>
  </section>
</template>

<script>
import {TicketService} from '@/service/api'
import {Code} from '@/commons/code'
export default {
  data () {
    return {
      tableData: [],
      total: 0
    }
  },
  filters: {
    ticketFlag: Code.ticketFlag,
    ticketStatus: Code.ticketStatus,
    ticketModule: Code.ticketModule
  },
  mounted: function () {
    this.queryTickets(1)
  },
  methods: {
    routeToCreate: function () {
      this.$router.push({
        path: '/user-center/suggestions'
      })
    },
    queryTickets: function (val) {
      TicketService.page(val, 10)
        .then(res => {
          if (res) {
            // console.log(res)
            this.tableData = res.tickets
            this.total = res.total
          }
        })
    },
    onView: function (item) {
      this.$router.push({
        path: `suggestions-view/${item.id}`
      })
    },
    onClose: function (item) {
      this.$router.push({
        path: `suggestions-view/${item.id}`
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .suggestion-list {
    .title {
      margin-right: 2em;
      display: inline-block;
    }
    .text-align {
      text-align: center;
      line-height: 3em;
    }
  }
</style>



