<template>
  <el-container class="account-home">
    <el-header>
      <el-button-group>
        <el-button type="primary" @click="toLogin()">去登录</el-button>
        <el-button type="default" @click="toRegister()">去注册</el-button>
        <el-button type="default" @click="onModifyPassword()">忘记密码</el-button>
      </el-button-group>
    </el-header>
    <el-main>
      <router-view/>
      <modify-password :visible.sync="modifyPassword"></modify-password>
    </el-main>
    <el-footer>更高的浏览器版本可以获取更好的用户体验哦~</el-footer>
  </el-container>
</template>

<script>
import ModifyPassword from './ModifyPassword'

export default {
  name: 'account-home',
  components: {
    'modify-password': ModifyPassword
  },
  data () {
    return {
      modifyPassword: false
    }
  },
  methods: {
    onModifyPassword: function () {
      this.modifyPassword = true
    },
    toLogin: function () {
      this.$router.push({path: '/account-home/login'})
    },
    toRegister: function () {
      this.$router.push({path: '/account-home/register'})
    }
  }

}
</script>

<style scoped lang="scss">
  .account-home {
    .el-header {
      background-color: #545c64;
      color: #333;
      line-height: 60px;
      text-align: right;
    }
    .el-main {
      height: calc(100vh - 120px);
      color: #333;
      text-align: center;
    }
    .el-footer {
      background-color: #545c64;
      color: white;
      line-height: 60px;
      text-align: center;
    }
  }
</style>



