import Vuex from 'vuex'
import Vue from 'vue'
import * as types from './types'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      uid: 0,
      level: 0,
      nickname: '',
      username: '',
      email: '',
      phone: '',
      address: '',
      birthday: 0
    },
    wallet: {
      coin: 0,
      coin_frozen: 0
    },
    jwt: null,
    title: null,
    exp: null // 单位是ms
  },
  mutations: {
    [types.LOGIN]: (state, data) => {
      if (localStorage.jwt !== data) {
        localStorage.setItem('jwt', data)
      }
      state.jwt = data
    },
    [types.LOGOUT]: (state) => {
      localStorage.removeItem('jwt')
      localStorage.removeItem('exp')
      localStorage.removeItem('user')
      state.jwt = null
      state.exp = null
    },
    [types.TITLE]: (state, data) => {
      state.title = data
    },
    [types.EXP]: (state, data) => {
      data = parseInt(data)
      if (new RegExp(`^[0-9]{10}$`).test(data.toString())) {
        data *= 1000
      }
      localStorage.exp = data
      state.exp = data
    },
    [types.USER]: (state, data) => {
      localStorage.setItem('user', JSON.stringify(data))
      state.user = data
    },
    [types.WALLET]: (state, data) => {
      state.wallet = data
    }
  }
})



