<template>
  <section class="user-panel">
    <h3>用户面板</h3>
    <header class="user-header">
      <div class="user-card-wrapper">
        <div class="user-card">
          <h2><span>{{wallet.coin}}</span> Coin</h2>
          <em @click="onRecharge()" class="link">立即充值</em>
        </div>
      </div>
      <div class="user-card-wrapper">
        <div class="user-card">
          <h2>Level {{user.level}}</h2>
          <em class="link">
            <router-link to="/user-center/purchase" class="link-text">去升级会员
            </router-link>
          </em>
        </div>
      </div>
      <div class="user-card-wrapper">
        <div class="user-card">
          <h2>我要推广</h2>
          <em>
            <el-popover
              placement="bottom"
              title="邀请码:"
              width="99"
              trigger="click"
              :content="inviteCode">
              <span slot="reference" @click="getInviteCode" class="link">生成邀请码</span>
            </el-popover>
          </em>
        </div>
      </div>
      <div class="user-card-wrapper">
        <div class="user-card">
          <h2><span>{{ticketInProcess}}</span> 处理中</h2>
          <em style="cursor: pointer;text-decoration: underline">
            <router-link to="/user-center/suggestions-list" class="link-text">我的工单
            </router-link>
          </em>
        </div>
      </div>
    </header>
    <main>
      <aside class="user-info ol-content-box">
        <div class="user-info-header">
          <div v-if="user.avatar" class="user-info-header-avatar"><img :src="user.avatar | imgUrlFormat"></div>
        </div>
        <p>
          <strong>{{user.gender === 1 ? '男生' : (user.gender === 2 ? '女生' : '性别保密')}}</strong>
          <a href="javascript:;" style="position: absolute;margin-left: 1em" @click="showUserInfoEditor">
            <small><i class="el-icon-edit-outline"></i>编辑资料</small>
          </a>
        </p>
        <div class="user-info-body">
          <p>
            <small>昵称：</small>
            <strong class="overflow">{{user.nickname}}</strong></p>
          <p>
            <small>省份：</small>
            <strong class="overflow">{{user.province}}</strong></p>
          <p>
            <small>生日：</small>
            <strong class="overflow">{{user.birthday | birthday}}</strong></p>
          <p>
            <small>地址：</small>
            <strong class="overflow">{{user.address}}</strong></p>
        </div>
      </aside>
      <div class="user-msg-wrapper">
        <div class="user-msg">
          <div class="header">
            <em>我的消息</em>
            <router-link to="/user-center/message/message-list" class="more">查看更多>>
            </router-link>
          </div>
          <el-row v-for="s in msgs" class="line" :key="s.id" :gutter="20">
            <el-col :span="16"><strong class="overflow cell">{{s.title}}&nbsp;</strong></el-col>
            <el-col :span="8"><span class="overflow cell">{{ s.created_at * 1000 | dateFormat }}</span></el-col>
          </el-row>
        </div>
        <div class="user-msg">
          <div class="header">
            <em>系统通知</em>
            <router-link to="/user-center/notifies/notify-list" class="more">查看更多>>
            </router-link>
          </div>
          <el-row v-for="s in notices" class="line" :key="s.id" :gutter="20">
            <el-col :span="9"><strong class="overflow cell">{{s.title}}&nbsp;</strong></el-col>
            <el-col :span="9"><span class="overflow cell">{{s.sub_title }}</span></el-col>
            <el-col :span="6"><span class="overflow cell">{{ s.updated_at * 1000 | dateFormat }}</span></el-col>
          </el-row>
        </div>
      </div>
    </main>
    <recharge :visible.sync="dialogVisilble">
    </recharge>
    <user-info-edit :visible.sync="userInfoEditVisible">
    </user-info-edit>
  </section>
</template>
<script>
import Recharge from './recharge/Recharge'
import UserInfoEdit from './user-info/UserInfoEdit'
import {TicketService, UserService, NoticeService, MessageService} from '@/service/api'

export default {
  name: 'user-panel',
  data () {
    return {
      notices: [],
      ticketInProcess: 0,
      dialogVisilble: false,
      userInfoEditVisible: false,
      inviteCode: null,
      msgs: []
    }
  },
  filters: {
    birthday: function (ms) {
      let date = new Date(ms *= 1000)
      const year = date.getFullYear()
      const month = date.getMonth() + 1
      const day = date.getDate()

      return [year, month, day].map((n) => {
        n = n.toString()
        return n[1] ? n : '0' + n
      }).join('-')
    }
  },
  components: {
    'recharge': Recharge,
    'user-info-edit': UserInfoEdit
  },
  created: function () {
    this.queryTicketInProcess()
    this.queryNotice()
    this.queryMessageToMe()
  },
  computed: {
    user: function () {
      return this.$store.state.user
    },
    wallet: function () {
      return this.$store.state.wallet
    }
  },
  methods: {
    showUserInfoEditor: function () {
      this.userInfoEditVisible = true
    },
    getInviteCode () {
      UserService.inviteCreate().then(res => {
        if (res) {
          this.inviteCode = res.code
        }
      })
    },
    queryTicketInProcess: function () {
      TicketService.inProcess()
        .then(res => {
          if (res) {
            this.ticketInProcess = res.total ? res.total : 0
          }
        })
    },
    queryNotice: function () {
      NoticeService.page(1, 5)
        .then(res => {
          if (res) {
            this.notices = res.notices
          }
        })
    },
    queryMessageToMe: function () {
      MessageService.pageToMe(1, 5)
        .then(res => {
          if (res) {
            this.msgs = res.messages
          }
        })
    },
    onload: function () {
      this.loading = false
    },
    onRecharge: function () {
      this.dialogVisilble = true
    }
  }
}
</script>

<style scoped lang="scss">
  .user-panel {
    height: 100%;
    overflow: auto;
    position: relative;
    .link {
      cursor: pointer;
      text-decoration: underline;
    }
    .user-header {
      display: flex;
      flex-wrap: wrap;
      .user-card-wrapper {
        width: 25%;
        min-width: 222px;
        .user-card {
          padding: 2em;
          border: 1px solid #eeeeee;
          margin: 1em;
          color: #fff;
        }
      }
      .user-card-wrapper:nth-child(1) {
        .user-card {
          background-color: #e84c3d;
        }
      }
      .user-card-wrapper:nth-child(2) {
        .user-card {
          background-color: #1abc9c;
        }
      }
      .user-card-wrapper:nth-child(3) {
        .user-card {
          background-color: #3598db;
        }
      }
      .user-card-wrapper:nth-child(4) {
        .user-card {
          background-color: #9a59b5;
        }
      }
    }
    .user-info {
      position: absolute;;
      width: 25em;
      text-align: center;
      .user-info-header {
        position: relative;
        display: inline-block;
        padding: 11%;
        overflow: hidden;
        border-radius: 50%;
        border: 1px dashed #ddd;
        width: 0;
        height: 0;
        .user-info-header-avatar {
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
      .user-info-body {
        display: inline-block;
        text-align: left;
        max-width: 100%;
        p {
          line-height: 1em;
          position: relative;
          small {
            position: absolute;
          }
          strong {
            display: inline-block;
            margin-left: 3em;
            width: auto !important;
          }
        }
      }
    }
    .user-msg-wrapper {
      margin-left: 27em;
      .user-msg {
        padding: 1em;
        border: 1px solid #eeeeee;
        margin: 1em;
        .header {
          padding-bottom: 1em;
          border-bottom: 1px solid #eeeeee;
          .more {
            float: right;
            font-size: 0.7em;
            color: #409EFF;
          }
        }
        .line {
          line-height: 3em;
          padding: 0 0.5em;
          margin: .5em 0;
          .cell {
            border-radius: 5px;
            background: #606266;
            padding: 0 .5em;
            color: white;
          }
        }
      }
    }
  }
</style>



