<template>
  <section class="choice">
    <div class="ol-content-box header">
      <strong>选择题</strong>
      <div v-if="pic" class="img-wrapper">
        <img :src="pic|imgUrlFormat" class="img">
      </div>
    </div>
    <div class="container">
      <div class="ol-content-box" v-for="(question, index) in questions"
           :class="{'current-question':questionNo === question.no}">
        <strong>{{question.stem}}</strong><br>
        <p :class="[type==='multiline' ? 'multiline-option' : 'option']" v-for="(value, key) in question.selections"
           @click.capture.prevent="onCheck(index,key)">
          <el-radio v-model="question.checkedAnswer" :label="key">
            &nbsp;
          </el-radio>
          <span class="option-text">
            <span v-show="!value.endsWith('.jpg')">
                {{value}}
            </span>
            <img v-if="value.endsWith('.jpg')" :src="value |imgUrlFormat" style="max-width: 100%;">
          </span>
        </p>
      </div>
    </div>
  </section>
</template>
<script>
// multiline 多题一组
// singleline 单题一组
const MULTI_LINE = 'multiline'
const SINGLE_LINE = 'singleline'
export default {
  props: {
    choices: [Object, Array],
    questionNo: Number
  },
  data () {
    return {
      pic: null,
      type: 'singleline',
      questions: []
    }
  },
  watch: {
    choices: function (newVal) {
      if (newVal) {
        !(newVal instanceof Array) && (newVal = [newVal])
        this.pic = newVal[0].pic
        this.type = newVal.length > 1 ? MULTI_LINE : SINGLE_LINE
        this.questions = newVal
      }
    }
  },
  methods: {
    onCheck: function (index, key) { // 代理
      if (this.questions[index].checkedAnswer !== key) {
        this.questions[index].checkedAnswer = key
        this.$set(this.questions, index, this.questions[index])
        this.$emit('check', this.questions[index].no)
        // TODO 焦点管理
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .choice {
    max-height: calc(100vh - 222px);
    overflow-y: auto;
    .header {
      .img-wrapper {
        text-align: center;
        .img {
          max-width: 100%;
        }
      }
    }
    .container {
      .option {
        border: 1px solid #eeeeee;
        padding: .5em;
        border-radius: 5px;
        white-space: nowrap;
        cursor: pointer;
        display: block;
        margin: .5em 0;
        .option-text {
          display: inline-block;
          white-space: normal;
          vertical-align: middle;
          font-size: 16px;
          margin-right: 2em;
        }
      }
      .multiline-option {
        display: inline-block;
        border: 1px solid #eeeeee;
        padding: 6px;
        border-radius: 5px;
        white-space: nowrap;
        cursor: pointer;
        margin: .5em 1em 0 0;
        .option-text {
          display: inline-block;
          white-space: normal;
          vertical-align: middle;
          font-size: 16px;
          margin-right: 32px;
        }
      }
      .option:hover {
        color: white;
        background: #409eff;
      }
      .multiline-option:hover {
        border-color: #409eff;
      }
      .current-question {
        border-color: #409eff;
      }
    }

  }
</style>



