<template>
  <section class="page-list">
    <header>
      <h3 class="title">我的消息<small>（点击箭头>查看详细内容）</small></h3>
    </header>
    <el-table :data="tableData" stripe height="calc(100vh - 211px)">
      <el-table-column type="expand">
        <template slot-scope="props">
          <h4>消息内容:</h4>
          {{props.row.title}}
        </template>
      </el-table-column>
      <el-table-column prop="title" label="消息内容" width="555">
      </el-table-column>
      <el-table-column label="消费日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.created_at * 1000 | dateFormat}}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      @current-change="queryMsgs"
      :total="total">
    </el-pagination>
  </section>
</template>

<script>
import {MessageService} from '@/service/api'

export default {
  data () {
    return {
      tableData: [],
      total: 0
    }
  },
  mounted: function () {
    this.queryMsgs(1)
  },
  methods: {
    queryMsgs: function (val) {
      MessageService.pageToMe(val, 10)
        .then(res => {
          if (res) {
            this.tableData = res.messages
            this.total = res.total
          } else {
            this.tableData = []
            this.total = 0
          }
        })
    }
  }
}
</script>

<style scoped lang="scss">
  .page-list {
    .title {
      margin-right: 2em;
      display: inline-block;
    }
  }
</style>



