<template>
  <aside class="count-down">
    <div class="ol-content-box">
      <section>
        <slot></slot>
        <p style="color: red;font-size: 2em;">{{hour}} : {{min}} : {{second}}</p>
      </section>
    </div>
  </aside>
</template>

<script>
export default {
  props: ['timer'],

  computed: {
    // 计算属性的 getter
    hour: function () {
      return this.timer.hour
    },
    min: function () {
      return this.timer.min
    },
    second: function () {
      return this.timer.second
    }
  }
}
</script>

<style scoped lang="scss">
  .count-down {
    text-align: center;
  }
</style>



