<template>
  <section class="online-exam-guide">
    <div class="ol-content-box">
      <section class="user">
        <aside class="user-img"><img v-if="user.avatar" :src="user.avatar | imgUrlFormat"></aside>
        <main class="user-info">
          <p>
            <small>姓名：</small>
            <strong>{{user && user.nickname}}</strong></p>
          <p>
            <small>手机：</small>
            <strong>{{user && user.phone}}</strong></p>
        </main>
        <count-down class="count-down" :timer="time">
          <strong>
            倒计时
          </strong>
        </count-down>
      </section>
    </div>
    <div class="exam-remark ol-content-box ">
      <main class="exam-remark-main ">
        <h3>*注：请尽量在良好稳定的网络环境下参加考试。</h3>
        <p>一、考试开始前30分钟，应试人员可凭准考证和本人有效身份证件进入指定考场，按座位号入座。入座后，须将准考证和身份证件放置在桌面右上角。</p>
        <p>二、严禁应试人员携带笔、移动存储设备、电脑、计算器、电子记事本、通讯工具和其他规定以外的电子用品或者与考试相关的资料进入座位。</p>
        <p>三、考试时间以考试系统计时器显示的时间为准。开考后，考生交卷、离场的时间按相关考务规定执行。</p>
        <p>四、应试人员须按照考试系统或监考人员提示的要求进行操作，不得擅自对计算机进行冷、热启动，不得擅自关闭电源或作出其他与考试无关的操作，违反者责任自负。</p>
        <p>五、应试人员在考场内须保持安静，独立作答，不得与其他应试人员交流讨论，不得要求监考人员解释试题，发生异常情况等问题时可举手询问。</p>
        <p>六、应试人员须自觉接受考试监督和检查，服从工作人员安排。</p>
        <p>七、应试人员不得以任何方式将考试相关信息带出考场，交卷后不得在考场附近逗留、谈论。</p>
        <p>八、若有违纪违规行为，应试人员须接受考试管理部门或工作人员按照《专业技术人员资格考试违纪违规行为处理规定》（人力资源和社会保障部令第31号）等规定作出的有关处理。</p>
        <footer style="text-align: center">
          <el-button type="danger" :disabled="!isEnd" :loading="!isEnd" @click="onStartExam()">
            阅读完毕，开始考试
          </el-button>
        </footer>
      </main>
    </div>
  </section>
</template>
<script>
import CountDown from './CountDown'
import Timer from '@/commons/count-down-timer'

export default {
  data () {
    return {
      allTimerId: null,
      time: {
        hour: 'x',
        min: 'xx',
        second: 'xx'
      },
      isEnd: false,
      user: this.$store.state.user
    }
  },
  created: function () {
    let allTimer = {
      // todo 上线后 时间需要修改
      offsetSecond: 3,
      callback: this.onAllTimeChanged,
      endCallback: this.onAllTimerEnd
    }
    this.allTimerId = Timer.add(allTimer)
    Timer.startCountDown()
  },
  destroyed: function () {
    Timer.remove(this.allTimerId)
  },
  components: {
    'count-down': CountDown
  },
  methods: {
    onAllTimerEnd: function (timing) {
      this.isEnd = true
    },
    onAllTimeChanged: function (timing) {
      this.time = timing
    },
    onStartExam: function () {
      this.$router.replace({path: `/exam-center/${this.$route.params.id}/exam-pre`})
    }
  }
}
</script>

<style scoped lang="scss">
  .online-exam-guide {
    .user {
      display: flex;
      justify-content: center;
      .user-img {
        display: inline-block;
        width: 9em;
        height: 9em;
        border-radius: 50%;
        overflow: hidden;
        border: 1px dashed #ddd;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .user-info {
        margin-left: 2em;
      }
      .count-down {
        margin-left: 111px;
        text-align: center;
      }
    }
    .exam-remark {
      text-align: center;
      .exam-remark-main {
        text-align: left;
        display: inline-block;
      }
    }
  }
</style>



