export class Code {
  static Low = 1
  static Medium = 2
  static High = 3
  static SuperHigh = 4

  static UserCommit = 1
  static AdminCommit = 2
  static Finished = 3

  static Finance = 1
  static Account = 2
  static Paper = 3
  static Suggestion = 4
  static Other = 5

  static Recharge = 1
  static Profit = 2
  static Purchase = 3
  static Withdraw = 4
  static Admin = 5
  static Frozen = 6

  static Man = 1
  static Woman = 2
  static Unknow = 0

  static Success = 'success'

  static ticketStatus (code) {
    switch (code) {
      case Code.UserCommit:
        return '用户回复'
      case Code.AdminCommit:
        return '客服回复'
      case Code.Finished:
        return '已完结'
      default:
        return '未知'
    }
  }

  static ticketFlag (code) {
    switch (code) {
      case Code.Low:
        return '低'
      case Code.Medium:
        return '中'
      case Code.High:
        return '高'
      case Code.SuperHigh:
        return '极高'
      default:
        return '未知'
    }
  }

  static ticketModule (code) {
    switch (code) {
      case Code.Finance:
        return '财务'
      case Code.Account:
        return '账户'
      case Code.Paper:
        return '试题'
      case Code.Suggestion:
        return '建议'
      case Code.Other:
        return '其他'
      default:
        return '未知'
    }
  }

  static rechargeStatus (code) {
    switch (code) {
      case Code.Success:
        return '支付成功'
      default:
        return '支付取消'
    }
  }

  static gender (code) {
    switch (code) {
      case Code.Man:
        return '男生'
      case Code.Woman:
        return '女生'
      default:
        return '保密'
    }
  }

  static walletLogStatus (code) {
    switch (code) {
      case Code.Recharge:
        return '充值'
      case Code.Profit:
        return '奖励'
      case Code.Purchase:
        return '购物'
      case Code.Withdraw:
        return '提现'
      case Code.Admin:
        return '客服操作'
      case Code.Frozen:
        return '冻结'
      default:
        return '未知'
    }
  }
}



