<template>
  <section class="suggestion">
    <header>
      <h3 style="margin-right: 2em;">创建工单</h3>
    </header>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px"
             style="min-width: 666px;width: 66%;">
      <el-form-item label="问题类型" prop="module">
        <el-select v-model="ruleForm.module" placeholder="请选择问题类型">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="紧急程度" prop="flag">
        <el-select v-model="ruleForm.flag" placeholder="请选择紧急程度">
          <el-option
            v-for="item in flags"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="标题" prop="title">
        <el-input v-model="ruleForm.title"></el-input>
      </el-form-item>
      <el-form-item label="内容" prop="content">
        <el-input type="textarea" v-model="ruleForm.content" rows="9"></el-input>
      </el-form-item>
      <el-form-item label="图片">
        <el-upload ref="uploader"
                   action="http://upload-z2.qiniup.com"
                   :data="token"
                   :limit="Number(5)"
                   :before-upload="beforeUpload"
                   list-type="picture-card">
          <i class="el-icon-plus"></i>
          <div slot="tip">最多上传 5 张图片</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">立即提交</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>
<script>
import {SecurityService, TicketService} from '@/service/api'
import {Code} from '@/commons/code'

const moduleOptions = [{
  value: Code.Finance,
  label: Code.ticketModule(Code.Finance)
}, {
  value: Code.Account,
  label: Code.ticketModule(Code.Account)
}, {
  value: Code.Paper,
  label: Code.ticketModule(Code.Paper)
}, {
  value: Code.Suggestion,
  label: Code.ticketModule(Code.Suggestion)
}, {
  value: Code.Other,
  label: Code.ticketModule(Code.Other)
}]
const flagOptions = [{
  value: Code.Low,
  label: Code.ticketFlag(Code.Low)
}, {
  value: Code.Medium,
  label: Code.ticketFlag(Code.Medium)
}, {
  value: Code.High,
  label: Code.ticketFlag(Code.High)
}, {
  value: Code.SuperHigh,
  label: Code.ticketFlag(Code.SuperHigh)
}]

export default {
  name: 'suggestions',
  data () {
    return {
      options: moduleOptions,
      flags: flagOptions,
      token: null,
      ruleForm: {
        title: '',
        module: null,
        content: '',
        flag: null,
        pics: []
      },
      rules: {
        module: [
          {required: true, message: '请选择问题类型', trigger: 'change'}
        ],
        flag: [
          {required: true, message: '请选择紧急程度', trigger: 'change'}
        ],
        title: [
          {required: true, message: '请输入标题', trigger: 'change'},
          {min: 4, max: 64, message: '长度在 4 到 64 个字符', trigger: 'change'}
        ],
        content: [
          {required: true, message: '请输入内容', trigger: 'change'},
          {min: 2, max: 1024, message: '长度在 2 到 1024 个字符', trigger: 'change'}
        ]
      }
    }
  },
  methods: {
    beforeUpload: function () {
      if (this.token) {
        this.token.resource_key = 'tmp/' + new Date() + '.jpg'
        return true
      }
      const self = this
      return new Promise(function (resolve, reject) {
        SecurityService.qiniuToken()
          .then(res => {
            if (res) {
              self.token = {
                token: res.qiniu,
                resource_key: 'tmp/' + new Date() + '.jpg'
              }
              resolve()
            } else {
              self.$message.error('上传令牌获取失败')
              reject(res)
            }
          })
      })
    },
    submitForm: function (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        let imgs = []
        for (let f of this.$refs.uploader.uploadFiles) {
          imgs.push(f.response.key)
        }
        this.ruleForm.pics = imgs
        TicketService.create(this.ruleForm)
          .then(res => {
            if (res) {
              this.$message.success('提交成功')
              this.$router.push({path: '/user-center/suggestions-list'})
            }
          })
      })
    }
  }
}

</script>

<style scoped>
  .suggestion {
    height: 100%;
  }
</style>



