<template>
  <section class="online-exam">
    <el-form :inline="true" :model="filter" class="demo-form-inline">
      <el-form-item label="试卷名称">
        <el-input v-model="filter.name" placeholder="类似于"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="queryPapers(1)">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table :data="tableData" stripe height="calc(100vh - 215px)">
      <el-table-column prop="name" label="试卷" width="433">
      </el-table-column>
      <el-table-column label="收录日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.created_at *1000 | dateFormat('YYYY-MM-DD')}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="onPreExam(scope.row)"
            type="text"
            size="small">
            进入考试<i class="el-icon-d-arrow-right"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      @current-change="queryPapers"
      :total="total">
    </el-pagination>
    <el-dialog title="考前练一练加油" :visible.sync="dialogVisible">
      <section class="text-align">
        <h2>{{curPaper ? curPaper.name : ''}}</h2>
        <el-tag>时间：<span>{{curPaper.created_at | dateFormat('YYYY-MM-DD')}}</span></el-tag>
        <el-tag type="success">考试时长 <span>{{curPaper ? curPaper.duration : ''}}</span>分钟</el-tag>
        <el-tag type="info">总分 <span></span>{{curPaper ? curPaper.total_score : ''}}</el-tag>
        <el-tag type="warning">合格分数 <span>{{curPaper ? curPaper.pass_score : ''}}</span></el-tag>
        <el-tag type="danger">试卷等级 <span>{{curPaper ? curPaper.min_level : ''}}</span></el-tag>
      </section>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button :disabled="user.level<curPaper.min_level" type="primary" @click="onStartExam()">
          {{user.level >= curPaper.min_level ? '进入考场' : '会员等级不足'}}
        </el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import {PaperService} from '@/service/api'

export default {
  data () {
    return {
      filter: {
        name: null
      },
      dialogVisible: false,
      tableData: [],
      total: 0,
      curPaper: {}
    }
  },
  mounted: function () {
    this.queryPapers(1)
  },
  computed: {
    user: function () {
      return this.$store.state.user
    }
  },
  methods: {
    queryPapers: function (val) {
      let filter = {
        num: val,
        size: 10,
        name_like: this.filter.name
      }
      PaperService.page(filter)
        .then(res => {
          if (res) {
            this.tableData = res.papers
            this.total = res.total
          }
        })
    },
    onPreExam: function (item) {
      this.dialogVisible = true
      this.curPaper = item
    },
    onStartExam: function () {
      this.$router.push({path: `/exam-center/${this.curPaper.id}/exam-guide`})
    }
  }
}
</script>

<style scoped lang="scss">
  .online-exam {
    .text-align {
      text-align: center;
      line-height: 3em;
    }
  }
</style>



