<template>
  <section class="online-exam-room">
    <aside class="count-down-aside">
      <count-down :timer="allTiming" v-on:timing-end="isEnd = true">
        <strong>
          本场总倒计时
        </strong>
      </count-down>
      <count-down :timer="partTiming" v-on:timing-end="isEnd = true">
        <strong>
          本部分倒计时
        </strong>
      </count-down>
    </aside>
    <div class="questions" :style="{marginLeft:marginLeft+'px'}">
      <choice :choices="curQuestion" :questionNo="curQuestionNo" @check="onChangeQuestionNo($event,true)"></choice>
      <el-card>
        <section style="text-align: right;">
          <el-button type="warning" icon="el-icon-question" round @click="onMarked()">标记</el-button>
          <el-button round @click="preQ()">上一题</el-button>
          <el-button type="primary" round @click="nextQ()">下一题</el-button>
        </section>
      </el-card>
    </div>
    <footer class="footer">
      <el-switch
        v-model="isAutoToggle"
        active-text="自动切换题目">
      </el-switch>
      <el-button-group>
        <el-button @click="onNextPart()" class="close-btn" :disabled="section.deduct">选择进入下一部分
        </el-button>
        <el-button type="danger" @click="onCloseExam()" class="close-btn" :disabled="section.deduct">交卷
        </el-button>
      </el-button-group>
    </footer>

    <answer-card :sections="paper.sections" :curQuestionNo="curQuestionNo" :section="section"
                 :activeSecId="curSectionId"
                 v-on:toggleQuestion="onToggleQuestion" v-on:toggleCard="toggleCard"
                 v-on:onMarked="onMarked"></answer-card>

    <el-dialog title="选择要进入的考试部分" :visible.sync="nextDialogVisible" width="455px" :close-on-click-modal="false"
               :close-on-press-escape="false" :show-close="false">
      <section class="text-align">
        <p class="option" v-for="section in paper.sections" @click.capture.prevent="onCheckSection(section.id)">
          <el-radio v-model="checkedSection" :label="section.id" :disabled="section.completed">
            &nbsp;
          </el-radio>
          <span class="option-text">
            {{section.name}}
          </span>
        </p>
      </section>
      <div slot="footer" class="dialog-footer">
        <el-button @click="nextDialogVisible = false" v-show="partTimerId">取 消</el-button>
        <el-button type="primary" @click="onConfirmSection();">进入考场</el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import Choice from './Choice'
import AnswerCard from './AnswerCard'
import CountDown from './CountDown'
import Timer from '@/commons/count-down-timer'
import {PaperService, ExamService} from '@/service/api'

export default {
  data () {
    return {
      allTimerId: null,
      partTimerId: null,
      allTiming: {
        hour: 'x',
        min: 'xx',
        second: 'xx'
      },
      partTiming: {
        hour: 'x',
        min: 'xx',
        second: 'xx'
      },
      marginLeft: 343,
      curQuestionNo: 0,
      curSectionId: null,
      paper: {},
      section: {},
      nextDialogVisible: false,
      checkedSection: null,
      curQuestion: {},
      isAutoToggle: false
    }
  },
  components: {
    'choice': Choice,
    'answer-card': AnswerCard,
    'count-down': CountDown
  },
  watch: {
    // 如果 `curSectionId` 发生改变，这个函数就会运行
    curSectionId: function (newSectionId, oldSectionId) {
      if (this.partTimerId) {
        Timer.remove(this.partTimerId)
      }
      for (let section of this.paper.sections) {
        if (section.id === newSectionId) {
          this.section = section
        }
        if (section.id === oldSectionId) {
          section.completed = true
        }
      }
      let partTimer = {
        // todo 上线后时间修改
        offsetSecond: this.section.duration * 60,
        // offsetSecond: 3,
        callback: this.onPartTimeChanged,
        endCallback: this.onPartTimerEnd
      }
      this.partTimerId = Timer.add(partTimer)
      Timer.startCountDown()
      this.updateCurQuestion()
    }
  },
  created: function () {
    PaperService.one(this.$route.params.id)
      .then(res => {
        if (res) {
          this.paper = res.paper
          let allTimer = {
            // todo 上线后时间修改 单位为分钟
            offsetSecond: res.paper.duration * 60,
            // offsetSecond: 120,
            callback: this.onAllTimeChanged,
            endCallback: this.onAllTimerEnd
          }
          this.allTimerId = Timer.add(allTimer)
          Timer.startCountDown()
          this.curSectionId = this.paper.sections[0].id
        } else {
          this.$message.error('获取试卷失败了哦，' + res)
        }
      })
  },
  beforeDestroy: function () {
    Timer.remove(this.partTimerId)
    Timer.remove(this.allTimerId)
  },
  methods: {
    updateCurQuestion: function () {
      if (this.section.group === 1) {
        let q = this.section.questions[this.curQuestionNo]
        typeof q.no !== 'number' && (q.no = this.curQuestionNo)
        this.curQuestion = q
        return
      }
      // 题目组形式

      let startIndex = this.curQuestionNo - this.curQuestionNo % this.section.group
      let qArr = this.section.questions.slice(startIndex, startIndex + this.section.group)
      qArr.forEach(function (q) {
        typeof q.no !== 'number' && (q.no = startIndex++)
      })
      this.curQuestion = qArr
    },
    toggleCard: function (isShowed) { // 控制答题卡位置
      if (isShowed) {
        this.marginLeft = 343
      } else {
        this.marginLeft = 222
      }
    },
    onPartTimerEnd: function (timing) { // 弹窗提示想进入的考试部分
      if (this.partTimerId) {
        Timer.remove(this.partTimerId)
        this.partTimerId = null
      }
      // if (this.nextDialogVisible) { //防止打开过程中计时结束 暂时没必要
      //   return
      // }
      //
      let isAllCompleted = this.paper.sections.every(function (item) {
        return item.completed
      })
      if (isAllCompleted) {
        this.doCorrectPaper()
        return
      }

      this.onNextPart()
    },
    onAllTimerEnd: function (timing) { // 强制交卷
      this.onCloseExam()
    },
    onAllTimeChanged: function (timing) {
      this.allTiming = timing
    },
    onPartTimeChanged: function (timing) {
      this.partTiming = timing
    },
    onNextPart: function () {
      this.nextDialogVisible = true
      this.section.completed = true
    },
    onMarked: function (val) {
      if (val && val.no) {
        this.section.questions[val.no - 1].marked = !this.section.questions[val.no - 1].marked
        this.$set(this.section.questions, val.no - 1, this.section.questions[val.no - 1])
      } else {
        this.section.questions[this.curQuestionNo].marked = !this.section.questions[this.curQuestionNo].marked
        this.$set(this.section.questions, this.curQuestionNo, this.section.questions[this.curQuestionNo])
      }
    },
    preQ: function () {
      if (this.curQuestionNo <= 0) {
        return
      }
      this.onChangeQuestionNo(this.curQuestionNo - 1)
    },
    nextQ: function () {
      this.onChangeQuestionNo(this.curQuestionNo + 1)
    },
    onChangeQuestionNo: function (val, fromChoice) {
      if (val >= this.section.questions.length || val < 0) {
        return
      }
      let toggle = false
      // 来自choice触发 且 开启自动切换 且 不能超过 题目最大数量 且 是题目组最后一道
      if (fromChoice && this.isAutoToggle && (val + 1 < this.section.questions.length) && val % this.section.group === (this.section.group - 1)) {
        toggle = val % this.section.group === (this.section.group - 1)
        ;(toggle && (this.curQuestionNo = val + 1)) || (this.curQuestionNo = val)
      } else {
        this.curQuestionNo = val
      }
      (!fromChoice || toggle) && this.updateCurQuestion()
    },
    onConfirmSection: function () {
      if (!this.checkedSection) {
        return
      }
      this.curSectionId = this.checkedSection
      this.nextDialogVisible = false
      this.checkedSection = null
      this.onChangeQuestionNo(0)
    },
    onCheckSection: function (id) {
      for (let section of this.paper.sections) {
        if (section.id === id) {
          if (section.completed) {
            return
          }
          break
        }
      }
      if (this.checkedSection !== id) {
        this.checkedSection = id
      }
    },
    onToggleQuestion: function (val) {
      if (val.sectionId && val.oldSectionId) {
        for (let section of this.paper.sections) {
          if (section.id === val.oldSectionId) { // 扣分制，不能主动切换section
            if (section.deduct) {
              this.$message.warning('当前部分不允许主动切换')
              return
            }
            break
          } else if (section.id === val.sectionId) { // 做过，不能主动切换section
            if (section.completed) {
              return
            }
            break
          }
        }
        this.$confirm(`确认进入该部分的考试？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'info'
        }).then(() => {
          this.curSectionId = val.sectionId
        })
      }
      this.onChangeQuestionNo(val.qNo)
    },

    onCloseExam: function () {
      this.$confirm('交卷后无法答题, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.doCorrectPaper()
      })
    },
    doCorrectPaper: function () {
      let userAnswers = Object.create(null)
      let allTimer = Timer.get(this.allTimerId)
      let finalScore = 0
      // 尽量以整数运算来避免小数运算 得分制
      for (let section of this.paper.sections) {
        let score = getSectionScoreAnswer(section, userAnswers)
        finalScore += parseFloat(score.toFixed(1))
      }
      let finalPaper = {
        pid: this.paper.id,
        user_answers: userAnswers,
        score: finalScore,
        duration: this.paper.duration - (allTimer.offsetSecond / 60).toFixed(0) || 1// 最少1分钟
      }

      ExamService.submit(finalPaper).then(
        (res) => {
          if (res && res.id) {
            this.$message.success('试卷提交成功')
            this.$router.replace({path: `/completed-exam-view/${res.id}`})
          }
        })
    }
  }
}

function getSectionScoreAnswer (section, userAnswers) {
  if (!section || !section.questions) {
    return 0
  }
  let correctedCount = 0
  if (section.deduct) { // 作对加分，做错扣分，不做不加不扣，不能负分
    for (let q of section.questions) {
      userAnswers[q.id] = q.checkedAnswer
      if (typeof q.checkedAnswer === 'string') { // 选了答案
        if (q.checkedAnswer === q.answer) {
          correctedCount += 1
        } else {
          correctedCount -= 1
        }
      }
    }
  } else { // 作对加分，不做或做错不得分
    for (let q of section.questions) {
      userAnswers[q.id] = q.checkedAnswer
      if (q.checkedAnswer === q.answer) {
        correctedCount += 1
      }
    }
  }
  correctedCount = correctedCount < 0 ? 0 : correctedCount
  return correctedCount * section.every_score
}
</script>

<style scoped lang="scss">
  .online-exam-room {
    position: relative;
    height: 100%;
    .questions {
      margin-right: 222px;
      margin-left: 343px;
      transition: all 0.25s;
    }
    .footer {
      position: fixed;
      bottom: 0;
      padding: 0 3em;
      right: 0;
      line-height: 55px;
      text-align: center;
      background: white;
      box-shadow: 0px 0px 11px black;
      .el-switch {
        display: inline-block;
        margin-right: 3em;
      }
    }
    .answer-card {
      position: absolute;
      top: 0;
      left: 0;
    }
    .count-down-aside {
      position: absolute;
      right: 22px;
    }
    .option {
      border: 1px solid #eeeeee;
      padding: 6px;
      border-radius: 5px;
      white-space: nowrap;
      cursor: pointer;
      .option-text {
        display: inline-block;
        white-space: normal;
        vertical-align: middle;
        font-size: 16px;
        margin-right: 32px;
      }
    }
    .option:hover {
      color: white;
      background: #409eff;
    }

  }
</style>



