<template>
  <section class="page-list">
    <header>
      <h3 class="title">系统通知
        <small>（点击箭头>查看详细内容）</small>
      </h3>
    </header>
    <el-table :data="tableData" stripe height="calc(100vh - 211px)" v-on:expand-change="onExpand">
      <el-table-column type="expand">
        <template slot-scope="props">
          <h3>{{props.row.title}}</h3>
          {{props.row.sub_title}}
        </template>
      </el-table-column>
      <el-table-column prop="title" label="标题" width="455">
      </el-table-column>
      <el-table-column label="创建日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.created_at * 1000 | dateFormat}}</span>
        </template>
      </el-table-column>
      <el-table-column label="更新日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.updated_at * 1000 | dateFormat}}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      @current-change="queryNotifies"
      :total="total">
    </el-pagination>
  </section>
</template>

<script>
import {NoticeService} from '@/service/api'

export default {
  data () {
    return {
      tableData: [],
      total: 0
    }
  },
  mounted: function () {
    this.queryNotifies(1)
  },
  methods: {
    queryNotifies: function (val) {
      NoticeService.page(val, 10)
        .then(res => {
          if (res) {
            this.tableData = res.notices
            this.total = res.total
          } else {
            this.tableData = []
            this.total = 0
          }
        })
    },
    onExpand: function (item, index) {
      console.log(item, index)
      NoticeService.one(item.id).then(res => {
        if (res && res.notice) {
          // this.$set
          // this.$set(this.tableData, this.curQuestionNo, this.section.questions[this.curQuestionNo])
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .page-list {
    .title {
      margin-right: 2em;
      display: inline-block;
    }
  }
</style>



